import React, { FunctionComponent } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import dayjs from 'dayjs';
import { dateToISOString } from '@navikt/sif-common-formik/lib';
import { Block, intlUtils } from '@navikt/fp-common';
import { Hovedknapp } from 'nav-frontend-knapper';
import Veilederpanel from 'nav-frontend-veilederpanel';
import useSknad from 'app/utils/hooks/useSknad';
import { formaterNavn } from 'app/utils/personUtils';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import VeilederNormal from 'app/assets/VeilederNormal';
import { getFlerbarnsuker } from 'app/steps/uttaksplan-info/utils/uttaksplanHarForMangeFlerbarnsuker';
import { isAdoptertAnnetBarn, isAdoptertBarn, isAdoptertStebarn } from 'app/context/types/Barn';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import { findEldsteDato, ISOStringToDate } from 'app/utils/dateUtils';
import SknadRoutes from 'app/routes/routes';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import actionCreator from 'app/context/action/actionCreator';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import { MorFarAdopsjonAnnenForelderHarRettIESUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import DekningsgradSprsml from '../sprsml/DekningsgradSprsml';
import { getDekningsgradFromString } from 'app/utils/getDekningsgradFromString';
import { lagUttaksplan } from 'app/utils/uttaksplan/lagUttaksplan';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { storeAppState } from 'app/utils/submitUtils';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { getAntallUker } from 'app/steps/uttaksplan-info/utils/stnadskontoer';
import { getHarAktivitetskravIPeriodeUtenUttak } from 'app/utils/uttaksplan/uttaksplanUtils';
import StartdatoAdopsjon, { finnStartdatoAdopsjon } from '../mor-far-adopsjon/StartdatoAdopsjon';
import { morFarAdopsjonAnnenForelderHarRettIESQuestionsConfig } from './morFarAdopsjonAnnenForelderHarRettIESQuestionsConfig';
import {
    MorFarAdopsjonAnnenForelderHarRettIESFormComponents,
    MorFarAdopsjonAnnenForelderHarRettIESFormData,
    MorFarAdopsjonAnnenForelderHarRettIESFormField,
} from './morFarAdopsjonAnnenForelderHarRettIESFormConfig';
import {
    getInitialMorFarAdopsjonAnnenForelderHarRettIESValues,
    mapMorFarAdopsjonAnnenForelderHarRettIESFormToState,
} from './morFarAdopsjonAnnenForelderHarRettIESUtils';
import AdopsjonStartdatoValg from '../mor-far-adopsjon/adopsjonStartdatoValg';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
}

const MorFarAdopsjonAnnenForelderHarRettIES: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer80DTO,
    tilgjengeligeStnadskontoer100DTO,
}) => {
    const intl = useIntl();
    const { state } = useForeldrepengesknadContext();
    const { skersituasjon, annenForelder, barn, dekningsgrad, erEndringssknad } = useSknad();
    const {
        person: { fornavn, mellomnavn, etternavn },
    } = useSkerinfo();
    const lagretUttaksplanInfo = useUttaksplanInfo<MorFarAdopsjonAnnenForelderHarRettIESUttaksplanInfo>();
    const erDeltUttak = true;
    const erAdopsjon = skersituasjon.situasjon === 'adopsjon';
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const familiehendelsesdatoDate = ISOStringToDate(familiehendelsesdato);
    const frsteUttaksdagNesteBarnsSak =
        state.barnFraNesteSak !== undefined ? state.barnFraNesteSak.startdatoFrsteStnadsperiode : undefined;

    const onValidSubmitHandler = (values: Partial<MorFarAdopsjonAnnenForelderHarRettIESFormData>) => {
        const submissionValues = mapMorFarAdopsjonAnnenForelderHarRettIESFormToState(values);
        const barnAdopsjonsdato = isAdoptertBarn(barn) ? barn.adopsjonsdato : undefined;
        const antallUker = getAntallUker(tilgjengeligeStnadskontoer[values.dekningsgrad! === '100' ? 100 : 80]);

        const startdato = finnStartdatoAdopsjon(
            values.startdatoAdopsjonValg!,
            values.annenStartdatoAdopsjon,
            dateToISOString(barnAdopsjonsdato),
            dateToISOString(ankomstdato),
            undefined
        );

        return [
            actionCreator.setAntallUkerIUttaksplan(antallUker),
            actionCreator.setUttaksplanInfo(submissionValues),
            actionCreator.setDekningsgrad(getDekningsgradFromString(values.dekningsgrad)),
            actionCreator.lagUttaksplanforslag(
                lagUttaksplan({
                    annenForelderErUfr: false,
                    erDeltUttak,
                    erEndringssknad,
                    erEnkelEndringssknad: erEndringssknad,
                    familiehendelsesdato: familiehendelsesdatoDate!,
                    frsteUttaksdagEtterSeksUker: Uttaksdagen(
                        Uttaksdagen(familiehendelsesdatoDate!).denneEllerNeste()
                    ).leggTil(30),
                    situasjon: skersituasjon.situasjon,
                    skerErFarEllerMedmor: erFarEllerMedmor,
                    skerHarMidlertidigOmsorg: false,
                    tilgjengeligeStnadskontoer:
                        tilgjengeligeStnadskontoer[getDekningsgradFromString(values.dekningsgrad)],
                    uttaksplanSkjema: {
                        startdatoPermisjon: startdato,
                        farSinFrsteUttaksdag: erFarEllerMedmor ? startdato : undefined,
                    },
                    bareFarMedmorHarRett: false,
                    termindato: undefined,
                    harAktivitetskravIPeriodeUtenUttak: getHarAktivitetskravIPeriodeUtenUttak({
                        erDeltUttak,
                        morHarRett: true,
                        skerErAleneOmOmsorg: false,
                    }),
                    annenForelderHarRettPForeldrepengerIES: true,
                    frsteUttaksdagNesteBarnsSak,
                })
            ),
        ];
    };

    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        SknadRoutes.UTTAKSPLAN,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );

    if (!erAdopsjon || !isAdoptertBarn(barn)) {
        return null;
    }

    const erSkerMor = !erFarEllerMedmor;
    const oppgittAnnenForelder = isAnnenForelderOppgitt(annenForelder) ? annenForelder : undefined;
    const navnAnnenPart = oppgittAnnenForelder
        ? formaterNavn(oppgittAnnenForelder.fornavn, oppgittAnnenForelder.etternavn, true)
        : '';

    const erDeltUttakINorge = false;
    const navnSker = formaterNavn(fornavn, etternavn, true, mellomnavn);
    const navnMor = erSkerMor ? navnSker : navnAnnenPart;
    const navnFarMedmor = erSkerMor ? navnAnnenPart : navnSker;
    const erAdoptertIUtlandet = isAdoptertAnnetBarn(barn) ? barn.adoptertIUtlandet : false;
    const ankomstdato = isAdoptertAnnetBarn(barn) ? barn.ankomstdato : undefined;
    const antallBarn = barn.antallBarn;
    const latestDate =
        ankomstdato !== undefined && barn.adopsjonsdato !== undefined
            ? dateToISOString(findEldsteDato([ankomstdato, barn.adopsjonsdato]))
            : barn.adopsjonsdato;

    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO
    );

    return (
        <MorFarAdopsjonAnnenForelderHarRettIESFormComponents.FormikWrapper
            initialValues={getInitialMorFarAdopsjonAnnenForelderHarRettIESValues(lagretUttaksplanInfo, dekningsgrad)}
            onSubmit={handleSubmit}
            renderForm={({ values: formValues }) => {
                const visibility = morFarAdopsjonAnnenForelderHarRettIESQuestionsConfig.getVisbility({
                    ...formValues,
                    erFarEllerMedmor,
                });

                return (
                    <MorFarAdopsjonAnnenForelderHarRettIESFormComponents.Form
                        includeButtons={false}
                        includeValidationSummary={true}
                    >
                        <Block
                            padBottom="l"
                            visible={visibility.isIncluded(
                                MorFarAdopsjonAnnenForelderHarRettIESFormField.dekningsgrad
                            )}
                        >
                            <DekningsgradSprsml
                                FormKomponent={MorFarAdopsjonAnnenForelderHarRettIESFormComponents}
                                dekningsgradFeltNavn={MorFarAdopsjonAnnenForelderHarRettIESFormField.dekningsgrad}
                                tilgjengeligeStnadskontoer={tilgjengeligeStnadskontoer}
                                erDeltUttak={erDeltUttakINorge}
                            />
                        </Block>
                        <Block
                            visible={visibility.isIncluded(
                                MorFarAdopsjonAnnenForelderHarRettIESFormField.startdatoAdopsjonValg
                            )}
                        >
                            <StartdatoAdopsjon valgtStartdatoAdopsjon={formValues.startdatoAdopsjonValg} />
                        </Block>
                        <Block
                            padBottom="l"
                            visible={
                                erAdopsjon &&
                                isAdoptertBarn(barn) &&
                                formValues.startdatoAdopsjonValg === AdopsjonStartdatoValg.ANNEN &&
                                dayjs(latestDate).isBefore(
                                    dayjs(
                                        finnStartdatoAdopsjon(
                                            formValues.startdatoAdopsjonValg!,
                                            undefined,
                                            dateToISOString(barn.adopsjonsdato),
                                            dateToISOString(ankomstdato),
                                            undefined
                                        )
                                    ),
                                    'day'
                                ) &&
                                !isAdoptertStebarn(barn) &&
                                !erDeltUttak
                            }
                        >
                            <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                <FormattedMessage
                                    id={
                                        erAdoptertIUtlandet === false
                                            ? 'uttaksplaninfo.info.ikkeAdoptertIUtlandet'
                                            : 'uttaksplaninfo.info.adoptertIUtlandet'
                                    }
                                />
                            </Veilederpanel>
                        </Block>
                        <Block padBottom="l" visible={antallBarn > 1 && formValues.startdatoAdopsjonValg !== undefined}>
                            <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                <FormattedMessage
                                    id="uttaksplaninfo.veileder.flerbarnsInformasjon.annenForelderHarRettIES"
                                    values={{
                                        uker: getFlerbarnsuker(formValues.dekningsgrad!, antallBarn),
                                        navnFar: navnFarMedmor,
                                        navnMor: navnMor,
                                    }}
                                />
                            </Veilederpanel>
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp disabled={isSubmitting} spinner={isSubmitting}>
                                {intlUtils(intl, 'sknad.gVidere')}
                            </Hovedknapp>
                        </Block>
                    </MorFarAdopsjonAnnenForelderHarRettIESFormComponents.Form>
                );
            }}
        />
    );
};

export default MorFarAdopsjonAnnenForelderHarRettIES;
