import React, { FunctionComponent } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import dayjs from 'dayjs';
import { dateToISOString, YesOrNo } from '@navikt/sif-common-formik/lib';
import { Block, intlUtils } from '@navikt/fp-common';
import { Hovedknapp } from 'nav-frontend-knapper';
import Veilederpanel from 'nav-frontend-veilederpanel';
import useSknad from 'app/utils/hooks/useSknad';
import { getNavnGenitivEierform, formaterNavn } from 'app/utils/personUtils';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { getInitialMorFarAdopsjonValues, mapMorFarAdopsjonFormToState } from './morFarAdopsjonUtils';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import VeilederNormal from 'app/assets/VeilederNormal';
import { Forelder } from 'app/types/Forelder';
import { getFlerbarnsuker } from 'app/steps/uttaksplan-info/utils/uttaksplanHarForMangeFlerbarnsuker';
import { isAdoptertAnnetBarn, isAdoptertBarn, isAdoptertStebarn } from 'app/context/types/Barn';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import { dateIsSameOrAfter, findEldsteDato, ISOStringToDate } from 'app/utils/dateUtils';
import {
    MorFarAdopsjonFormComponents,
    MorFarAdopsjonFormData,
    MorFarAdopsjonFormField,
} from './morFarAdopsjonFormConfig';
import { morFarAdopsjonQuestionsConfig } from './morFarAdopsjonQuestionsConfig';
import { getTilgjengeligeDager } from '../../tilgjengeligeDagerGraf/tilgjengeligeDagerUtils';
import TilgjengeligeDagerGraf from '../../tilgjengeligeDagerGraf/TilgjengeligeDagerGraf';
import StartdatoAdopsjon, { finnStartdatoAdopsjon } from './StartdatoAdopsjon';
import MorsSisteDagSprsml from '../sprsml/MorsSisteDagSprsml';
import FarMedmorsFrsteDag from '../sprsml/FarMedmorsFrsteDag';
import AntallUkerOgDagerFellesperiodeFarMedmorSprsml from '../sprsml/AntallUkerOgDagerFellesperiodeFarMedmorSprsml';
import FordelingFellesperiodeSprsml from '../../fordelingFellesperiode/FordelingFellesperiodeSprsml';
import SknadRoutes from 'app/routes/routes';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import actionCreator from 'app/context/action/actionCreator';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import { MorFarAdopsjonUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import DekningsgradSprsml from '../sprsml/DekningsgradSprsml';
import { getDekningsgradFromString } from 'app/utils/getDekningsgradFromString';
import { lagUttaksplan } from 'app/utils/uttaksplan/lagUttaksplan';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { storeAppState } from 'app/utils/submitUtils';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { getAntallUker } from 'app/steps/uttaksplan-info/utils/stnadskontoer';
import AdopsjonStartdatoValg from './adopsjonStartdatoValg';
import { getHarAktivitetskravIPeriodeUtenUttak } from 'app/utils/uttaksplan/uttaksplanUtils';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
}

const MorFarAdopsjon: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer80DTO,
    tilgjengeligeStnadskontoer100DTO,
}) => {
    const intl = useIntl();
    const { state } = useForeldrepengesknadContext();
    const {
        skersituasjon,
        annenForelder,
        barn,
        sker: { erAleneOmOmsorg },
        dekningsgrad,
        erEndringssknad,
    } = useSknad();
    const {
        person: { fornavn, mellomnavn, etternavn },
    } = useSkerinfo();
    const lagretUttaksplanInfo = useUttaksplanInfo<MorFarAdopsjonUttaksplanInfo>();

    const erAdopsjon = skersituasjon.situasjon === 'adopsjon';
    const skerErAleneOmOmsorg = !!erAleneOmOmsorg;
    const annenForelderOppgittIkkeAleneOmOmsorg = isAnnenForelderOppgitt(annenForelder)
        ? annenForelder.harRettPForeldrepengerINorge !== undefined ||
          annenForelder.harRettPForeldrepengerIES !== undefined
        : false;
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const bareFarMedmorHarRett =
        erFarEllerMedmor &&
        isAnnenForelderOppgitt(annenForelder) &&
        !skerErAleneOmOmsorg &&
        !annenForelder.harRettPForeldrepengerINorge &&
        !annenForelder.harRettPForeldrepengerIES;
    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const familiehendelsesdatoDate = ISOStringToDate(familiehendelsesdato);
    const frsteUttaksdagNesteBarnsSak =
        state.barnFraNesteSak !== undefined ? state.barnFraNesteSak.startdatoFrsteStnadsperiode : undefined;

    const shouldRender =
        erAdopsjon && (annenForelderOppgittIkkeAleneOmOmsorg || annenForelder.kanIkkeOppgis || skerErAleneOmOmsorg);

    const onValidSubmitHandler = (values: Partial<MorFarAdopsjonFormData>) => {
        const submissionValues = mapMorFarAdopsjonFormToState(values);
        const barnAdopsjonsdato = isAdoptertBarn(barn) ? barn.adopsjonsdato : undefined;
        const antallUker = getAntallUker(tilgjengeligeStnadskontoer[values.dekningsgrad! === '100' ? 100 : 80]);

        const startdato = finnStartdatoAdopsjon(
            values.startdatoAdopsjonValg!,
            values.annenStartdatoAdopsjon,
            dateToISOString(barnAdopsjonsdato),
            dateToISOString(ankomstdato),
            values.skersFrsteDag
        );

        return [
            actionCreator.setAntallUkerIUttaksplan(antallUker),
            actionCreator.setUttaksplanInfo(submissionValues),
            actionCreator.setDekningsgrad(getDekningsgradFromString(values.dekningsgrad)),
            actionCreator.lagUttaksplanforslag(
                lagUttaksplan({
                    annenForelderErUfr: erMorUfr,
                    erDeltUttak,
                    erEndringssknad,
                    erEnkelEndringssknad: erEndringssknad,
                    familiehendelsesdato: familiehendelsesdatoDate!,
                    frsteUttaksdagEtterSeksUker: Uttaksdagen(
                        Uttaksdagen(familiehendelsesdatoDate!).denneEllerNeste()
                    ).leggTil(30),
                    situasjon: skersituasjon.situasjon,
                    skerErFarEllerMedmor: erFarEllerMedmor,
                    skerHarMidlertidigOmsorg: false,
                    tilgjengeligeStnadskontoer:
                        tilgjengeligeStnadskontoer[getDekningsgradFromString(values.dekningsgrad)],
                    uttaksplanSkjema: {
                        fellesperiodeukerMor: submissionValues.fellesperiodeukerMor,
                        startdatoPermisjon: startdato,
                        antallDagerFellesperiodeFarMedmor: parseInt(submissionValues.antallDagerFellesperiode),
                        antallUkerFellesperiodeFarMedmor: parseInt(submissionValues.antallUkerFellesperiode),
                        harAnnenForelderSktFP: submissionValues.harAnnenForelderSktFP,
                        morSinSisteUttaksdag: submissionValues.annenForeldersSisteDag,
                        farSinFrsteUttaksdag: submissionValues.skersFrsteDag,
                    },
                    bareFarMedmorHarRett: bareFarMedmorHarRett,
                    termindato: undefined,
                    harAktivitetskravIPeriodeUtenUttak: getHarAktivitetskravIPeriodeUtenUttak({
                        erDeltUttak,
                        morHarRett: !bareFarMedmorHarRett,
                        skerErAleneOmOmsorg,
                    }),
                    frsteUttaksdagNesteBarnsSak,
                })
            ),
        ];
    };

    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        SknadRoutes.UTTAKSPLAN,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );

    if (!shouldRender || !isAdoptertBarn(barn)) {
        return null;
    }

    const erSkerMor = !erFarEllerMedmor;

    const oppgittAnnenForelder = isAnnenForelderOppgitt(annenForelder) ? annenForelder : undefined;
    const harAnnenForelderRettPForeldrepengerINorge = !!oppgittAnnenForelder?.harRettPForeldrepengerINorge;
    const fornavnAnnenForeldre = oppgittAnnenForelder?.fornavn;
    const erAnnenPartUfr = !!oppgittAnnenForelder?.erUfr;
    const navnAnnenPart = oppgittAnnenForelder
        ? formaterNavn(oppgittAnnenForelder.fornavn, oppgittAnnenForelder.etternavn, true)
        : '';

    const erDeltUttak = isAnnenForelderOppgitt(annenForelder)
        ? !!annenForelder.harRettPForeldrepengerINorge || !!annenForelder.harRettPForeldrepengerIES
        : false;

    const erMorUfr = erSkerMor ? false : erAnnenPartUfr;

    const navnSker = formaterNavn(fornavn, etternavn, true, mellomnavn);
    const navnMor = erSkerMor ? navnSker : navnAnnenPart;
    const navnFarMedmor = erSkerMor ? navnAnnenPart : navnSker;

    const erAdoptertIUtlandet = isAdoptertAnnetBarn(barn) ? barn.adoptertIUtlandet : false;
    const ankomstdato = isAdoptertAnnetBarn(barn) ? barn.ankomstdato : undefined;
    const antallBarn = barn.antallBarn;
    const latestDate =
        ankomstdato !== undefined && barn.adopsjonsdato !== undefined
            ? dateToISOString(findEldsteDato([ankomstdato, barn.adopsjonsdato])) // todo - sjekk logikk her
            : barn.adopsjonsdato;

    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO
    );

    return (
        <MorFarAdopsjonFormComponents.FormikWrapper
            initialValues={getInitialMorFarAdopsjonValues(lagretUttaksplanInfo, dekningsgrad)}
            onSubmit={handleSubmit}
            renderForm={({ values: formValues, setFieldValue }) => {
                const visibility = morFarAdopsjonQuestionsConfig.getVisbility({
                    ...formValues,
                    harAnnenForelderRettPForeldrepengerINorge,
                    erAleneOmOmsorg,
                });

                const valgtStnadskonto = tilgjengeligeStnadskontoer[formValues.dekningsgrad === '100' ? 100 : 80];

                const tilgjengeligeDager = valgtStnadskonto
                    ? getTilgjengeligeDager(valgtStnadskonto, false, Forelder.farMedmor)
                    : undefined;

                return (
                    <MorFarAdopsjonFormComponents.Form includeButtons={false} includeValidationSummary={true}>
                        <Block
                            padBottom="l"
                            visible={visibility.isIncluded(MorFarAdopsjonFormField.harAnnenForelderSktFP)}
                        >
                            <MorFarAdopsjonFormComponents.YesOrNoQuestion
                                name={MorFarAdopsjonFormField.harAnnenForelderSktFP}
                                legend={intlUtils(intl, 'uttaksplaninfo.sprsml.harAnnenForelderSktFP.label', {
                                    navnAnnenForelder: fornavnAnnenForeldre,
                                })}
                            />
                        </Block>
                        {formValues.harAnnenForelderSktFP === YesOrNo.YES && (
                            <Block padBottom="l">
                                <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                    <FormattedMessage
                                        id="uttaksplaninfo.informasjon.tilAnnenForelder"
                                        values={{
                                            navn: getNavnGenitivEierform(fornavnAnnenForeldre!, intl.locale),
                                        }}
                                    />
                                </Veilederpanel>
                            </Block>
                        )}
                        <Block padBottom="l" visible={visibility.isIncluded(MorFarAdopsjonFormField.dekningsgrad)}>
                            <DekningsgradSprsml
                                FormKomponent={MorFarAdopsjonFormComponents}
                                dekningsgradFeltNavn={MorFarAdopsjonFormField.dekningsgrad}
                                tilgjengeligeStnadskontoer={tilgjengeligeStnadskontoer}
                                erDeltUttak={erDeltUttak}
                            />
                        </Block>
                        <Block padBottom="l" visible={visibility.isAnswered(MorFarAdopsjonFormField.dekningsgrad)}>
                            {tilgjengeligeDager && (
                                <TilgjengeligeDagerGraf
                                    erDeltUttak={erDeltUttak}
                                    erFarEllerMedmor={!erSkerMor}
                                    navnFarMedmor={navnFarMedmor}
                                    navnMor={navnMor}
                                    tilgjengeligeDager={tilgjengeligeDager}
                                />
                            )}
                        </Block>
                        <Block visible={visibility.isIncluded(MorFarAdopsjonFormField.startdatoAdopsjonValg)}>
                            <StartdatoAdopsjon valgtStartdatoAdopsjon={formValues.startdatoAdopsjonValg} />
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isIncluded(MorFarAdopsjonFormField.annenForeldersSisteDag)}
                        >
                            <MorsSisteDagSprsml
                                FormComponents={MorFarAdopsjonFormComponents}
                                fieldName={MorFarAdopsjonFormField.annenForeldersSisteDag}
                                navnMor={navnAnnenPart}
                                familiehendelsesdato={familiehendelsesdato}
                            />
                        </Block>
                        <Block padBottom="l" visible={visibility.isIncluded(MorFarAdopsjonFormField.skersFrsteDag)}>
                            <FarMedmorsFrsteDag
                                FormComponents={MorFarAdopsjonFormComponents}
                                fieldName={MorFarAdopsjonFormField.skersFrsteDag}
                                familiehendelsesdato={familiehendelsesdatoDate!}
                                setFieldValue={setFieldValue}
                                morsSisteDag={ISOStringToDate(formValues.annenForeldersSisteDag)}
                                navnMor={navnMor}
                                termindato={undefined}
                                situasjon={skersituasjon.situasjon}
                                morHarRettTilForeldrepengerIES={false}
                            />
                        </Block>
                        <Block
                            padBottom="l"
                            visible={
                                visibility.isAnswered(MorFarAdopsjonFormField.skersFrsteDag) &&
                                !dateIsSameOrAfter(
                                    ISOStringToDate(formValues.annenForeldersSisteDag),
                                    ISOStringToDate(formValues.skersFrsteDag)
                                ) &&
                                formValues.harAnnenForelderSktFP === YesOrNo.YES
                            }
                        >
                            {tilgjengeligeDager && (
                                <AntallUkerOgDagerFellesperiodeFarMedmorSprsml
                                    FormComponents={MorFarAdopsjonFormComponents}
                                    ukerFieldName={MorFarAdopsjonFormField.antallUkerFellesperiode}
                                    dagerFieldName={MorFarAdopsjonFormField.antallDagerFellesperiode}
                                    antallDager={formValues.antallDagerFellesperiode}
                                    antallUker={formValues.antallUkerFellesperiode}
                                    setFieldValue={setFieldValue}
                                    ukerMedFellesperiode={tilgjengeligeDager.dagerFelles / 5}
                                />
                            )}
                        </Block>
                        <Block
                            padBottom="l"
                            visible={
                                formValues.startdatoAdopsjonValg === AdopsjonStartdatoValg.ANNEN &&
                                dayjs(latestDate).isBefore(
                                    dayjs(
                                        finnStartdatoAdopsjon(
                                            formValues.startdatoAdopsjonValg,
                                            formValues.annenStartdatoAdopsjon,
                                            dateToISOString(barn.adopsjonsdato),
                                            dateToISOString(ankomstdato),
                                            formValues.skersFrsteDag
                                        )
                                    ),
                                    'day'
                                ) &&
                                !isAdoptertStebarn(barn) &&
                                !erDeltUttak
                            }
                        >
                            <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                <FormattedMessage
                                    id={
                                        erAdoptertIUtlandet === false
                                            ? 'uttaksplaninfo.info.ikkeAdoptertIUtlandet'
                                            : 'uttaksplaninfo.info.adoptertIUtlandet'
                                    }
                                />
                            </Veilederpanel>
                        </Block>
                        <Block visible={erAleneOmOmsorg === false && harAnnenForelderRettPForeldrepengerINorge}>
                            <Block
                                padBottom="l"
                                visible={
                                    antallBarn > 1 &&
                                    formValues.startdatoAdopsjonValg !== undefined &&
                                    formValues.harAnnenForelderSktFP !== YesOrNo.YES
                                }
                            >
                                <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                    <FormattedMessage
                                        id="uttaksplaninfo.veileder.flerbarnsInformasjon"
                                        values={{
                                            uker: getFlerbarnsuker(formValues.dekningsgrad!, antallBarn),
                                            navnFar: navnFarMedmor,
                                            navnMor: navnMor,
                                        }}
                                    />
                                </Veilederpanel>
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isIncluded(MorFarAdopsjonFormField.fellesperiodeukerMor)}
                            >
                                <FordelingFellesperiodeSprsml
                                    setFieldValue={setFieldValue}
                                    valgtStnadskonto={valgtStnadskonto}
                                    valgtFellesperiodeukerMor={formValues.fellesperiodeukerMor}
                                    mor={navnMor}
                                    farMedmor={navnFarMedmor}
                                    annenForelderErFarEllerMedmor={!erSkerMor}
                                />
                            </Block>
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp disabled={isSubmitting} spinner={isSubmitting}>
                                {intlUtils(intl, 'sknad.gVidere')}
                            </Hovedknapp>
                        </Block>
                    </MorFarAdopsjonFormComponents.Form>
                );
            }}
        />
    );
};

export default MorFarAdopsjon;
