import React, { FunctionComponent } from 'react';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import useSknad from 'app/utils/hooks/useSknad';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { getFamiliehendelsedato, getFdselsdato, getTermindato } from 'app/utils/barnUtils';
import {
    FarMedmorFdselOgMorHarIkkeRettFormComponents,
    FarMedmorFdselOgMorHarIkkeRettFormData,
    FarMedmorFdselOgMorHarIkkeRettFormField,
} from './farMedmorFdselOgMorHarIkkeRettFormConfig';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import {
    getInitialFarMedmorFdselOgMorHarIkkeRettValues,
    mapFarMedmorFdselOgMorHarIkkeRettFormToState,
} from './farMedmorFdselOgMorHarIkkeRettUtils';
import { FarMedmorFdselOgMorHarIkkeRettUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import actionCreator from 'app/context/action/actionCreator';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import SknadRoutes from 'app/routes/routes';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import { Block, intlUtils } from '@navikt/fp-common';
import { FormattedMessage, useIntl } from 'react-intl';
import { farMedmorFdselOgMorHarIkkeRettQuestionsConfig } from './farMedmorFdselOgMorHarIkkeRettQuestionsConfig';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import TilgjengeligeDagerGraf from '../../tilgjengeligeDagerGraf/TilgjengeligeDagerGraf';
import { getTilgjengeligeDager } from '../../tilgjengeligeDagerGraf/tilgjengeligeDagerUtils';
import { Forelder } from 'app/types/Forelder';
import { formaterNavn } from 'app/utils/personUtils';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import Veilederpanel from 'nav-frontend-veilederpanel';
import VeilederNormal from 'app/assets/VeilederNormal';
import { DateRange, dateToISOString } from '@navikt/sif-common-formik/lib';
import { Tidsperioden } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { uttaksplanDatoavgrensninger } from 'app/steps/uttaksplan-info/utils/uttaksplanDatoavgrensninger';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { DatepickerDateRange } from 'nav-datovelger';
import { Hovedknapp } from 'nav-frontend-knapper';
import { validateStartdatoFarMedmor } from './validation/farMedmorFdselOgMorHarIkkeRettValidering';
import DekningsgradSprsml from '../sprsml/DekningsgradSprsml';
import { getDekningsgradFromString } from 'app/utils/getDekningsgradFromString';
import { lagUttaksplan } from 'app/utils/uttaksplan/lagUttaksplan';
import { storeAppState } from 'app/utils/submitUtils';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { andreAugust2022ReglerGjelder, ISOStringToDate } from 'app/utils/dateUtils';
import { getErMorUfr } from 'app/utils/annenForelderUtils';
import { getHarAktivitetskravIPeriodeUtenUttak } from 'app/utils/uttaksplan/uttaksplanUtils';
import { skalViseInfoOmPrematuruker } from 'app/utils/uttaksplanInfoUtils';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';

const konverterStringTilDate = (invalidDateRanges?: DatepickerDateRange[]): DateRange[] | undefined => {
    if (!invalidDateRanges) {
        return undefined;
    }

    return invalidDateRanges.map((r) => ({
        from: ISOStringToDate(r.from)!,
        to: ISOStringToDate(r.to)!,
    }));
};

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
}

const FarMedmorFdselOgMorHarIkkeRett: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer80DTO,
    tilgjengeligeStnadskontoer100DTO,
}) => {
    const intl = useIntl();
    const { state } = useForeldrepengesknadContext();
    const { skersituasjon, annenForelder, barn, dekningsgrad, erEndringssknad } = useSknad();
    const {
        person: { fornavn, mellomnavn, etternavn },
    } = useSkerinfo();
    const lagretUttaksplanInfo = useUttaksplanInfo<FarMedmorFdselOgMorHarIkkeRettUttaksplanInfo>();

    const frsteUttaksdagNesteBarnsSak =
        state.barnFraNesteSak !== undefined ? state.barnFraNesteSak.startdatoFrsteStnadsperiode : undefined;
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const erFdsel = skersituasjon.situasjon === 'fdsel';
    const annenForelderHarIkkeRett = isAnnenForelderOppgitt(annenForelder)
        ? annenForelder.harRettPForeldrepengerINorge === false && annenForelder.harRettPForeldrepengerIES === false
        : false;
    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const familiehendelsesdatoDate = ISOStringToDate(familiehendelsesdato);
    const termindato = getTermindato(barn);

    const onValidSubmitHandler = (values: Partial<FarMedmorFdselOgMorHarIkkeRettFormData>) => {
        const startDatoUttaksplan =
            andreAugust2022ReglerGjelder(familiehendelsesdatoDate!) || getErMorUfr(annenForelder, erFarEllerMedmor)
                ? values.permisjonStartdato
                : dateToISOString(Uttaksdagen(Uttaksdagen(familiehendelsesdatoDate!).denneEllerNeste()).leggTil(30));

        return [
            actionCreator.setUttaksplanInfo(mapFarMedmorFdselOgMorHarIkkeRettFormToState(values)),
            actionCreator.setDekningsgrad(getDekningsgradFromString(values.dekningsgrad)),
            actionCreator.lagUttaksplanforslag(
                lagUttaksplan({
                    annenForelderErUfr: erMorUfr,
                    erDeltUttak: false,
                    erEndringssknad,
                    erEnkelEndringssknad: erEndringssknad,
                    familiehendelsesdato: familiehendelsesdatoDate!,
                    frsteUttaksdagEtterSeksUker: Uttaksdagen(
                        Uttaksdagen(familiehendelsesdatoDate!).denneEllerNeste()
                    ).leggTil(30),
                    situasjon: skersituasjon.situasjon,
                    skerErFarEllerMedmor: erFarEllerMedmor,
                    skerHarMidlertidigOmsorg: false,
                    tilgjengeligeStnadskontoer:
                        tilgjengeligeStnadskontoer[getDekningsgradFromString(values.dekningsgrad)],
                    uttaksplanSkjema: {
                        startdatoPermisjon: startDatoUttaksplan,
                    },
                    bareFarMedmorHarRett: true,
                    termindato,
                    harAktivitetskravIPeriodeUtenUttak: getHarAktivitetskravIPeriodeUtenUttak({
                        erDeltUttak: false,
                        morHarRett: false,
                        skerErAleneOmOmsorg: false,
                    }),
                    frsteUttaksdagNesteBarnsSak,
                })
            ),
        ];
    };

    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        SknadRoutes.UTTAKSPLAN,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );

    const shouldRender = erFarEllerMedmor && erFdsel && annenForelderHarIkkeRett;

    if (!shouldRender) {
        return null;
    }

    const erDeltUttak = !annenForelderHarIkkeRett;
    const erMorUfr = isAnnenForelderOppgitt(annenForelder) ? !!annenForelder.erUfr : false;
    const navnMor = isAnnenForelderOppgitt(annenForelder)
        ? formaterNavn(annenForelder.fornavn, annenForelder.etternavn, true)
        : '';
    const navnFarMedmor = formaterNavn(fornavn, etternavn, true, mellomnavn);
    const datoAvgrensinger = uttaksplanDatoavgrensninger.startdatoPermisjonFarMedmor(
        familiehendelsesdatoDate!,
        termindato,
        skersituasjon.situasjon
    );
    const fdselsdato = getFdselsdato(barn);
    const visInfoOmPrematuruker = skalViseInfoOmPrematuruker(fdselsdato, termindato, skersituasjon.situasjon);
    const ekstraDagerGrunnetPrematurFdsel = visInfoOmPrematuruker
        ? Tidsperioden({ fom: fdselsdato!, tom: termindato! }).getAntallUttaksdager() - 1
        : undefined;

    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO
    );

    return (
        <FarMedmorFdselOgMorHarIkkeRettFormComponents.FormikWrapper
            initialValues={getInitialFarMedmorFdselOgMorHarIkkeRettValues(dekningsgrad, lagretUttaksplanInfo)}
            onSubmit={handleSubmit}
            renderForm={({ values: formValues }) => {
                const visibility = farMedmorFdselOgMorHarIkkeRettQuestionsConfig.getVisbility({
                    ...formValues,
                    erMorUfr,
                    familiehendelsesdato: familiehendelsesdatoDate!,
                });

                const valgtStnadskonto = tilgjengeligeStnadskontoer[formValues.dekningsgrad === '100' ? 100 : 80];

                return (
                    <FarMedmorFdselOgMorHarIkkeRettFormComponents.Form
                        includeButtons={false}
                        includeValidationSummary={true}
                    >
                        <Block
                            padBottom="l"
                            visible={visibility.isIncluded(FarMedmorFdselOgMorHarIkkeRettFormField.dekningsgrad)}
                        >
                            <DekningsgradSprsml
                                FormKomponent={FarMedmorFdselOgMorHarIkkeRettFormComponents}
                                dekningsgradFeltNavn={FarMedmorFdselOgMorHarIkkeRettFormField.dekningsgrad}
                                tilgjengeligeStnadskontoer={tilgjengeligeStnadskontoer}
                                erDeltUttak={erDeltUttak}
                            />
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isAnswered(FarMedmorFdselOgMorHarIkkeRettFormField.dekningsgrad)}
                        >
                            {valgtStnadskonto && (
                                <TilgjengeligeDagerGraf
                                    erDeltUttak={erDeltUttak}
                                    erFarEllerMedmor
                                    navnFarMedmor={navnFarMedmor}
                                    navnMor={navnMor}
                                    tilgjengeligeDager={getTilgjengeligeDager(
                                        valgtStnadskonto,
                                        erDeltUttak,
                                        Forelder.farMedmor
                                    )}
                                />
                            )}
                        </Block>
                        <Block padBottom="l" visible={visInfoOmPrematuruker === true}>
                            <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                <FormattedMessage
                                    id="uttaksplaninfo.veileder.informasjonPrematuruker"
                                    values={{
                                        antallprematuruker: Math.floor(ekstraDagerGrunnetPrematurFdsel! / 5),
                                        antallprematurdager: ekstraDagerGrunnetPrematurFdsel! % 5,
                                    }}
                                />
                            </Veilederpanel>
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isIncluded(FarMedmorFdselOgMorHarIkkeRettFormField.permisjonStartdato)}
                        >
                            <FarMedmorFdselOgMorHarIkkeRettFormComponents.DatePicker
                                name={FarMedmorFdselOgMorHarIkkeRettFormField.permisjonStartdato}
                                label={intlUtils(intl, 'uttaksplaninfo.sprsml.startdatoPermisjonFarMedmor.label')}
                                minDate={ISOStringToDate(datoAvgrensinger.minDate)}
                                maxDate={ISOStringToDate(datoAvgrensinger.maxDate)}
                                disabledDateRanges={konverterStringTilDate(datoAvgrensinger.invalidDateRanges)}
                                disableWeekend={datoAvgrensinger.weekendsNotSelectable}
                                validate={validateStartdatoFarMedmor(
                                    intl,
                                    ISOStringToDate(datoAvgrensinger.minDate)!,
                                    ISOStringToDate(datoAvgrensinger.maxDate)!
                                )}
                                placeholder={'dd.mm.'}
                            />
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp disabled={isSubmitting} spinner={isSubmitting}>
                                {intlUtils(intl, 'sknad.gVidere')}
                            </Hovedknapp>
                        </Block>
                    </FarMedmorFdselOgMorHarIkkeRettFormComponents.Form>
                );
            }}
        />
    );
};

export default FarMedmorFdselOgMorHarIkkeRett;
