import { MorFarAdopsjonAnnenForelderHarRettIESUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import { finnEnum } from '../mor-far-adopsjon/adopsjonStartdatoValg';
import {
    MorFarAdopsjonAnnenForelderHarRettIESFormField,
    MorFarAdopsjonAnnenForelderHarRettIESFormData,
} from './morFarAdopsjonAnnenForelderHarRettIESFormConfig';

const initialMorFarAdopsjonAnnenForelderHarRettIESValues: MorFarAdopsjonAnnenForelderHarRettIESFormData = {
    [MorFarAdopsjonAnnenForelderHarRettIESFormField.dekningsgrad]: '',
    [MorFarAdopsjonAnnenForelderHarRettIESFormField.startdatoAdopsjonValg]: undefined,
    [MorFarAdopsjonAnnenForelderHarRettIESFormField.annenStartdatoAdopsjon]: '',
};
export const mapMorFarAdopsjonAnnenForelderHarRettIESFormToState = (
    values: Partial<MorFarAdopsjonAnnenForelderHarRettIESFormData>
): MorFarAdopsjonAnnenForelderHarRettIESUttaksplanInfo => {
    return {
        dekningsgrad: values.dekningsgrad!,
        startdatoAdopsjonValg: values.startdatoAdopsjonValg!,
        annenStartdatoAdopsjon: values.annenStartdatoAdopsjon!,
    };
};
export const getInitialMorFarAdopsjonAnnenForelderHarRettIESValues = (
    lagretUttaksplanInfo: MorFarAdopsjonAnnenForelderHarRettIESUttaksplanInfo | undefined,
    dekningsgrad: Dekningsgrad
): MorFarAdopsjonAnnenForelderHarRettIESFormData => {
    if (lagretUttaksplanInfo) {
        return {
            ...lagretUttaksplanInfo,
            [MorFarAdopsjonAnnenForelderHarRettIESFormField.dekningsgrad]: dekningsgrad,
            [MorFarAdopsjonAnnenForelderHarRettIESFormField.startdatoAdopsjonValg]: finnEnum(
                lagretUttaksplanInfo.startdatoAdopsjonValg
            ),
        };
    }
    return initialMorFarAdopsjonAnnenForelderHarRettIESValues;
};
