import { EksisterendeSak } from 'app/types/EksisterendeSak';

export type UttaksplanInfoScenario =
    | 'farMedmorAleneomsorgFdselAdopsjon'
    | 'farMedmorFdselBeggeHarRett'
    | 'farMedmorFdselMorHarIkkeRett'
    | 'farMedmorFrstegangssknadMedAnnenPart'
    | 'morFarAdopsjon'
    | 'morFdsel'
    | 'morFarFdselAnnenForelderHarRettIES'
    | 'morFarAdopsjonAnnenForelderHarRettIES';

interface UttaksplanScenarioParams {
    erFdsel: boolean;
    erFarEllerMedmor: boolean;
    skerErAleneOmOmsorg: boolean;
    annenForelderKanIkkeOppgis: boolean;
    annenForelderHarRett: boolean;
    erAdopsjon: boolean;
    eksisterendeSakAnnenPart: EksisterendeSak | undefined;
    annenForelderOppgittIkkeAleneOmOmsorg: boolean;
    annenForelderHarRettIES: boolean;
}

export const getUttaksplanScenario = ({
    erFdsel,
    erFarEllerMedmor,
    skerErAleneOmOmsorg,
    annenForelderKanIkkeOppgis,
    annenForelderHarRett,
    eksisterendeSakAnnenPart,
    erAdopsjon,
    annenForelderOppgittIkkeAleneOmOmsorg,
    annenForelderHarRettIES,
}: UttaksplanScenarioParams): UttaksplanInfoScenario => {
    if (erFdsel && erFarEllerMedmor && (skerErAleneOmOmsorg || annenForelderKanIkkeOppgis)) {
        return 'farMedmorAleneomsorgFdselAdopsjon';
    }
    if (annenForelderHarRettIES) {
        return erFdsel ? 'morFarFdselAnnenForelderHarRettIES' : 'morFarAdopsjonAnnenForelderHarRettIES';
    }

    if (
        erFarEllerMedmor &&
        erFdsel &&
        annenForelderHarRett &&
        (eksisterendeSakAnnenPart === undefined || eksisterendeSakAnnenPart.uttaksplan.length === 0)
    ) {
        return 'farMedmorFdselBeggeHarRett';
    }

    if (erFarEllerMedmor && erFdsel && !annenForelderHarRett) {
        return 'farMedmorFdselMorHarIkkeRett';
    }

    if (eksisterendeSakAnnenPart && erFarEllerMedmor) {
        return 'farMedmorFrstegangssknadMedAnnenPart';
    }

    if (erAdopsjon && (annenForelderOppgittIkkeAleneOmOmsorg || annenForelderKanIkkeOppgis || skerErAleneOmOmsorg)) {
        return 'morFarAdopsjon';
    }

    return 'morFdsel';
};
