import { sorterPerioder } from 'app/steps/uttaksplan-info/utils/Periodene';
import { getTidsperiode, Tidsperioden } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { Forelder } from 'app/types/Forelder';
import { Situasjon } from 'app/types/Situasjon';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { guid } from 'nav-frontend-js-utils';
import { splittPeriodePDato, splittUttaksperiodePFamiliehendelsesdato } from 'uttaksplan/builder/leggTilPeriode';
import { isUttaksperiode, Periode, Periodetype, Uttaksperiode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { andreAugust2022ReglerGjelder, tidperiodeOverlapperDato } from '../dateUtils';
import { farMedmorsTidsperiodeSkalSplittesPFamiliehendelsesdato } from '../wlbUtils';

const ikkeDeltUttakAdopsjonFarMedmor = (
    famDato: Date,
    foreldrepengerKonto: TilgjengeligStnadskonto,
    startdatoPermisjon: Date | undefined,
    erMorUfr: boolean | undefined,
    aktivitetsfriKvote: TilgjengeligStnadskonto | undefined,
    bareFarMedmorHarRett: boolean,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    const frsteUttaksdag = Uttaksdagen(startdatoPermisjon || famDato).denneEllerNeste();
    const perioder: Uttaksperiode[] = [];

    if (erMorUfr !== true) {
        let startDatoNestePeriode = frsteUttaksdag;
        if (andreAugust2022ReglerGjelder(famDato) && !!bareFarMedmorHarRett) {
            const aktivitetsFriPeriode: Uttaksperiode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder: Forelder.farMedmor,
                konto: StnadskontoType.AktivitetsfriKvote,
                tidsperiode: getTidsperiode(frsteUttaksdag, aktivitetsfriKvote!.dager),
                vedlegg: [],
                gradert: false,
                harIkkeAktivitetskrav: true,
            };
            if (
                frsteUttaksdagNesteBarnsSak !== undefined &&
                tidperiodeOverlapperDato(aktivitetsFriPeriode.tidsperiode, frsteUttaksdagNesteBarnsSak)
            ) {
                const splittetPeriode = splittPeriodePDato(
                    aktivitetsFriPeriode,
                    frsteUttaksdagNesteBarnsSak
                ) as Uttaksperiode[];
                splittetPeriode.forEach((sp) => perioder.push(sp));
            } else {
                perioder.push(aktivitetsFriPeriode);
            }
            startDatoNestePeriode = Uttaksdagen(aktivitetsFriPeriode.tidsperiode.tom).neste();
        }
        const periode: Uttaksperiode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.farMedmor,
            konto: foreldrepengerKonto.konto,
            tidsperiode: getTidsperiode(startDatoNestePeriode, foreldrepengerKonto.dager),
            vedlegg: [],
            gradert: false,
        };
        if (
            frsteUttaksdagNesteBarnsSak !== undefined &&
            tidperiodeOverlapperDato(periode.tidsperiode, frsteUttaksdagNesteBarnsSak)
        ) {
            const splittetPeriode = splittPeriodePDato(periode, frsteUttaksdagNesteBarnsSak) as Uttaksperiode[];
            splittetPeriode.forEach((sp) => perioder.push(sp));
        } else {
            perioder.push(periode);
        }
    } else {
        const aktivitetsFriPeriode: Uttaksperiode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.farMedmor,
            konto: StnadskontoType.AktivitetsfriKvote,
            tidsperiode: getTidsperiode(frsteUttaksdag, aktivitetsfriKvote!.dager),
            vedlegg: [],
            gradert: false,
            harIkkeAktivitetskrav: true,
        };

        if (
            frsteUttaksdagNesteBarnsSak !== undefined &&
            tidperiodeOverlapperDato(aktivitetsFriPeriode.tidsperiode, frsteUttaksdagNesteBarnsSak)
        ) {
            const splittetPeriode = splittPeriodePDato(
                aktivitetsFriPeriode,
                frsteUttaksdagNesteBarnsSak
            ) as Uttaksperiode[];
            splittetPeriode.forEach((sp) => perioder.push(sp));
        } else {
            perioder.push(aktivitetsFriPeriode);
        }

        const aktivitetskravPeriode: Uttaksperiode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.farMedmor,
            konto: StnadskontoType.Foreldrepenger,
            tidsperiode: getTidsperiode(
                Uttaksdagen(aktivitetsFriPeriode.tidsperiode.tom).neste(),
                foreldrepengerKonto.dager
            ),
            vedlegg: [],
            gradert: false,
        };

        if (
            frsteUttaksdagNesteBarnsSak !== undefined &&
            tidperiodeOverlapperDato(aktivitetskravPeriode.tidsperiode, frsteUttaksdagNesteBarnsSak)
        ) {
            const splittetPeriode = splittPeriodePDato(
                aktivitetskravPeriode,
                frsteUttaksdagNesteBarnsSak
            ) as Uttaksperiode[];
            splittetPeriode.forEach((sp) => perioder.push(sp));
        } else {
            perioder.push(aktivitetskravPeriode);
        }
    }

    return perioder;
};

const ikkeDeltUttakAdopsjonMor = (
    famDato: Date,
    foreldrepengerKonto: TilgjengeligStnadskonto,
    startdatoPermisjon: Date | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    const frsteUttaksdag = Uttaksdagen(startdatoPermisjon || famDato).denneEllerNeste();
    const periode: Uttaksperiode = {
        id: guid(),
        type: Periodetype.Uttak,
        forelder: Forelder.mor,
        konto: foreldrepengerKonto.konto,
        tidsperiode: getTidsperiode(frsteUttaksdag, foreldrepengerKonto.dager),
        vedlegg: [],
        gradert: false,
    };
    if (
        frsteUttaksdagNesteBarnsSak !== undefined &&
        tidperiodeOverlapperDato(periode.tidsperiode, frsteUttaksdagNesteBarnsSak)
    ) {
        return splittPeriodePDato(periode, frsteUttaksdagNesteBarnsSak);
    }
    return [periode];
};

const ikkeDeltUttakAdopsjon = (
    famDato: Date,
    erFarEllerMedmor: boolean,
    foreldrepengerKonto: TilgjengeligStnadskonto,
    startdatoPermisjon: Date | undefined,
    erMorUfr: boolean | undefined,
    aktivitetsfriKvote: TilgjengeligStnadskonto | undefined,
    bareFarMedmorHarRett: boolean,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    if (!erFarEllerMedmor) {
        return ikkeDeltUttakAdopsjonMor(famDato, foreldrepengerKonto, startdatoPermisjon, frsteUttaksdagNesteBarnsSak);
    } else {
        return ikkeDeltUttakAdopsjonFarMedmor(
            famDato,
            foreldrepengerKonto,
            startdatoPermisjon,
            erMorUfr,
            aktivitetsfriKvote,
            bareFarMedmorHarRett,
            frsteUttaksdagNesteBarnsSak
        );
    }
};

const ikkeDeltUttakFdselMor = (
    famDato: Date,
    foreldrepengerKonto: TilgjengeligStnadskonto,
    startdatoPermisjon: Date | undefined,
    foreldrePengerFrFdselKonto: TilgjengeligStnadskonto
) => {
    const frsteUttaksdag = Uttaksdagen(famDato).denneEllerNeste();
    const perioder: Periode[] = [];
    const skalHaForeldrePengerFrFdsel = startdatoPermisjon ? true : false;

    if (foreldrePengerFrFdselKonto !== undefined && skalHaForeldrePengerFrFdsel && startdatoPermisjon) {
        const dagerFrFdsel = Uttaksdagen(startdatoPermisjon).getUttaksdagerFremTilDato(frsteUttaksdag);
        const merEnnTreUkerPermisjonFrFdsel = dagerFrFdsel > 15;
        const startdatoFpFrFdsel = Uttaksdagen(frsteUttaksdag).trekkFra(
            merEnnTreUkerPermisjonFrFdsel ? 15 : dagerFrFdsel
        );

        if (merEnnTreUkerPermisjonFrFdsel) {
            const ekstraPeriodeFrFdsel: Periode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder: Forelder.mor,
                konto: StnadskontoType.Foreldrepenger,
                tidsperiode: getTidsperiode(startdatoPermisjon, dagerFrFdsel - 15),
                vedlegg: [],
            };

            perioder.push(ekstraPeriodeFrFdsel);
        }

        const periodeFrFdsel: Periode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.mor,
            konto: foreldrePengerFrFdselKonto.konto,
            tidsperiode: {
                fom: startdatoFpFrFdsel,
                tom: Uttaksdagen(frsteUttaksdag).forrige(),
            },
            vedlegg: [],
        };

        perioder.push(periodeFrFdsel);
    } else {
        const periodeFrFdsel: Periode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.mor,
            konto: StnadskontoType.ForeldrepengerFrFdsel,
            skalIkkeHaUttakFrTermin: true,
            tidsperiode: {} as any,
            vedlegg: [],
        };

        perioder.push(periodeFrFdsel);
    }

    const ekstraPermisjonFrFdsel = perioder.find(
        (p) => isUttaksperiode(p) && p.konto === StnadskontoType.Foreldrepenger
    );

    const antallDagerIForeldrepenger = ekstraPermisjonFrFdsel
        ? getTidsperiode(
              frsteUttaksdag,
              foreldrepengerKonto.dager - Tidsperioden(ekstraPermisjonFrFdsel.tidsperiode).getAntallUttaksdager()
          )
        : getTidsperiode(frsteUttaksdag, foreldrepengerKonto.dager);

    const foreldrepengerPeriode: Periode = {
        id: guid(),
        type: Periodetype.Uttak,
        forelder: Forelder.mor,
        konto: foreldrepengerKonto.konto,
        tidsperiode: antallDagerIForeldrepenger,
        vedlegg: [],
        gradert: false,
    };

    perioder.push(foreldrepengerPeriode);

    return perioder.sort(sorterPerioder);
};

const ikkeDeltUttakFdselFarMedmor = (
    famDato: Date,
    foreldrepengerKonto: TilgjengeligStnadskonto,
    startdatoPermisjon: Date | undefined,
    erMorUfr: boolean | undefined,
    aktivitetsfriKvote: TilgjengeligStnadskonto | undefined,
    bareFarMedmorHarRett: boolean,
    termindato: Date | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    const startDato = Uttaksdagen(startdatoPermisjon || famDato).denneEllerNeste();
    const morHarRett = false;
    const perioder: Periode[] = [];

    if (erMorUfr !== true) {
        let startDatoNestePeriode = startDato;
        if (andreAugust2022ReglerGjelder(famDato) && !!bareFarMedmorHarRett) {
            const aktivitetsFriPeriode: Uttaksperiode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder: Forelder.farMedmor,
                konto: StnadskontoType.AktivitetsfriKvote,
                tidsperiode: getTidsperiode(startDato, aktivitetsfriKvote!.dager),
                vedlegg: [],
                harIkkeAktivitetskrav: true,
            };
            if (
                farMedmorsTidsperiodeSkalSplittesPFamiliehendelsesdato(
                    aktivitetsFriPeriode,
                    famDato,
                    morHarRett,
                    termindato
                )
            ) {
                const aktivitetsFriePerioder = splittUttaksperiodePFamiliehendelsesdato(aktivitetsFriPeriode, famDato);
                aktivitetsFriePerioder.forEach((periode) => perioder.push(periode));
            } else if (
                frsteUttaksdagNesteBarnsSak !== undefined &&
                tidperiodeOverlapperDato(aktivitetsFriPeriode.tidsperiode, frsteUttaksdagNesteBarnsSak)
            ) {
                const aktivitetsFriePerioder = splittPeriodePDato(aktivitetsFriPeriode, frsteUttaksdagNesteBarnsSak);
                aktivitetsFriePerioder.forEach((periode) => perioder.push(periode));
            } else {
                perioder.push(aktivitetsFriPeriode);
            }
            startDatoNestePeriode = Uttaksdagen(aktivitetsFriPeriode.tidsperiode.tom).neste();
        }

        const periode: Uttaksperiode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.farMedmor,
            konto: foreldrepengerKonto.konto,
            tidsperiode: getTidsperiode(startDatoNestePeriode, foreldrepengerKonto.dager),
            vedlegg: [],
            gradert: false,
        };

        perioder.push(periode);
    } else {
        const aktivitetsFriPeriode: Uttaksperiode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.farMedmor,
            konto: StnadskontoType.AktivitetsfriKvote,
            tidsperiode: getTidsperiode(startDato, aktivitetsfriKvote!.dager),
            vedlegg: [],
            gradert: false,
            harIkkeAktivitetskrav: true,
        };

        if (
            farMedmorsTidsperiodeSkalSplittesPFamiliehendelsesdato(
                aktivitetsFriPeriode,
                famDato,
                morHarRett,
                termindato
            )
        ) {
            const aktivitetsFriePerioder = splittUttaksperiodePFamiliehendelsesdato(aktivitetsFriPeriode, famDato);
            aktivitetsFriePerioder.forEach((periode) => perioder.push(periode));
        } else {
            perioder.push(aktivitetsFriPeriode);
        }

        const aktivitetskravPeriode: Uttaksperiode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.farMedmor,
            konto: StnadskontoType.Foreldrepenger,
            tidsperiode: getTidsperiode(
                Uttaksdagen(aktivitetsFriPeriode.tidsperiode.tom).neste(),
                foreldrepengerKonto.dager
            ),
            vedlegg: [],
            gradert: false,
        };
        perioder.push(aktivitetskravPeriode);
    }

    return perioder.sort(sorterPerioder);
};

const ikkeDeltUttakFdsel = (
    famDato: Date,
    erFarEllerMedmor: boolean,
    foreldrepengerKonto: TilgjengeligStnadskonto,
    startdatoPermisjon: Date | undefined,
    foreldrePengerFrFdselKonto: TilgjengeligStnadskonto | undefined,
    erMorUfr: boolean | undefined,
    aktivitetsfriKvote: TilgjengeligStnadskonto | undefined,
    bareFarMedmorHarRett: boolean,
    termindato: Date | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    if (!erFarEllerMedmor) {
        return ikkeDeltUttakFdselMor(famDato, foreldrepengerKonto, startdatoPermisjon, foreldrePengerFrFdselKonto!);
    } else {
        return ikkeDeltUttakFdselFarMedmor(
            famDato,
            foreldrepengerKonto,
            startdatoPermisjon,
            erMorUfr,
            aktivitetsfriKvote,
            bareFarMedmorHarRett,
            termindato,
            frsteUttaksdagNesteBarnsSak
        );
    }
};

export const ikkeDeltUttak = (
    situasjon: Situasjon,
    famDato: Date,
    erFarEllerMedmor: boolean,
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    startdatoPermisjon: Date | undefined,
    erMorUfr: boolean | undefined,
    bareFarMedmorHarRett: boolean,
    termindato: Date | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    const foreldrepengerKonto = tilgjengeligeStnadskontoer.find(
        (konto) => konto.konto === StnadskontoType.Foreldrepenger
    );
    const foreldrePengerFrFdselKonto = tilgjengeligeStnadskontoer.find(
        (konto) => konto.konto === StnadskontoType.ForeldrepengerFrFdsel
    );
    const aktivitetsfriKvote = tilgjengeligeStnadskontoer.find(
        (konto) => konto.konto === StnadskontoType.AktivitetsfriKvote
    );

    if (situasjon === 'adopsjon') {
        return ikkeDeltUttakAdopsjon(
            famDato,
            erFarEllerMedmor,
            foreldrepengerKonto!,
            startdatoPermisjon,
            erMorUfr,
            aktivitetsfriKvote,
            bareFarMedmorHarRett,
            frsteUttaksdagNesteBarnsSak
        );
    }

    if (situasjon === 'fdsel') {
        return ikkeDeltUttakFdsel(
            famDato,
            erFarEllerMedmor,
            foreldrepengerKonto!,
            startdatoPermisjon,
            foreldrePengerFrFdselKonto,
            erMorUfr,
            aktivitetsfriKvote,
            bareFarMedmorHarRett,
            termindato,
            frsteUttaksdagNesteBarnsSak
        );
    }

    return [];
};
