import { sorterPerioder } from 'app/steps/uttaksplan-info/utils/Periodene';
import { getTidsperiode, Tidsperioden } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { Forelder } from 'app/types/Forelder';
import { Situasjon } from 'app/types/Situasjon';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { UtsettelsersakType } from 'uttaksplan/types/UtsettelsersakType';
import { guid } from 'nav-frontend-js-utils';
import { isUttaksperiode, Periode, Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import {
    andreAugust2022ReglerGjelder,
    dateIsSameOrAfter,
    skalFarUtsetteEtterMorSinSisteUttaksdag,
    tidperiodeOverlapperDato,
} from '../dateUtils';
import {
    farMedmorsTidsperiodeSkalSplittesPFamiliehendelsesdato,
    getLengdePForeslttWLBUttakFarMedmor,
    starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel,
} from '../wlbUtils';
import { splittPeriodePDato, splittUttaksperiodePFamiliehendelsesdato } from 'uttaksplan/builder/leggTilPeriode';

const deltUttakAdopsjonSktFrst = (
    famDato: Date,
    erFarEllerMedmor: boolean,
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    startdatoPermisjon: Date | undefined,
    fellesperiodeukerMor: number | undefined,
    harAnnenForelderSktFP: boolean | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined
): Periode[] => {
    if (harAnnenForelderSktFP !== true) {
        const frsteUttaksdag = Uttaksdagen(startdatoPermisjon || famDato).denneEllerNeste();
        const perioder: Periode[] = [];
        const kontoType = erFarEllerMedmor ? StnadskontoType.Fedrekvote : StnadskontoType.Mdrekvote;
        const forelder = erFarEllerMedmor ? Forelder.farMedmor : Forelder.mor;
        const konto: TilgjengeligStnadskonto | undefined = tilgjengeligeStnadskontoer.find((k) =>
            erFarEllerMedmor ? k.konto === StnadskontoType.Fedrekvote : k.konto === StnadskontoType.Mdrekvote
        );
        let currentTomDate: Date = frsteUttaksdag;

        if (konto !== undefined) {
            const periodeMdrekvote: Periode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder,
                konto: kontoType,
                tidsperiode: getTidsperiode(currentTomDate, konto.dager),
                nskerSamtidigUttak: false,
                gradert: false,
            };

            currentTomDate = Uttaksdagen(periodeMdrekvote.tidsperiode.tom).neste();

            if (
                frsteUttaksdagNesteBarnsSak !== undefined &&
                tidperiodeOverlapperDato(periodeMdrekvote.tidsperiode, frsteUttaksdagNesteBarnsSak)
            ) {
                const splittedePerioder = splittPeriodePDato(periodeMdrekvote, frsteUttaksdagNesteBarnsSak);
                splittedePerioder.forEach((periode) => perioder.push(periode));
            } else {
                perioder.push(periodeMdrekvote);
            }
        }

        if (fellesperiodeukerMor !== undefined && fellesperiodeukerMor > 0) {
            const periodeFellesperiode: Periode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder,
                konto: StnadskontoType.Fellesperiode,
                tidsperiode: getTidsperiode(currentTomDate, fellesperiodeukerMor * 5),
                nskerSamtidigUttak: false,
                gradert: false,
            };
            if (
                frsteUttaksdagNesteBarnsSak !== undefined &&
                tidperiodeOverlapperDato(periodeFellesperiode.tidsperiode, frsteUttaksdagNesteBarnsSak)
            ) {
                const splittedePerioder = splittPeriodePDato(periodeFellesperiode, frsteUttaksdagNesteBarnsSak);
                splittedePerioder.forEach((periode) => perioder.push(periode));
            } else {
                perioder.push(periodeFellesperiode);
            }
        }

        return perioder.sort(sorterPerioder);
    } else {
        return [];
    }
};

const deltUttakAdopsjonSktSist = (
    familiehendelsesdato: Date,
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    erFarEllerMedmor: boolean,
    antallDagerFellesperiodeFarMedmor: number | undefined,
    antallUkerFellesperiodeFarMedmor: number | undefined,
    morSinSisteUttaksdag: Date,
    farSinFrsteUttaksdag: Date,
    begrunnelseForUtsettelse: UtsettelsersakType | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    if (erFarEllerMedmor) {
        // Oppfrer seg identisk som fdselsscenario
        return deltUttakFdselFarMedmor(
            tilgjengeligeStnadskontoer,
            antallDagerFellesperiodeFarMedmor,
            antallUkerFellesperiodeFarMedmor,
            morSinSisteUttaksdag,
            farSinFrsteUttaksdag,
            begrunnelseForUtsettelse,
            familiehendelsesdato,
            undefined,
            undefined,
            frsteUttaksdagNesteBarnsSak,
            'adopsjon'
        );
    } else {
        const forslag = deltUttakFdselFarMedmor(
            tilgjengeligeStnadskontoer,
            antallDagerFellesperiodeFarMedmor,
            antallUkerFellesperiodeFarMedmor,
            morSinSisteUttaksdag,
            farSinFrsteUttaksdag,
            begrunnelseForUtsettelse,
            familiehendelsesdato,
            undefined,
            undefined,
            frsteUttaksdagNesteBarnsSak,
            'adopsjon'
        );

        const forslagGjortOmTilMor = forslag.map((periode) => {
            if (
                isUttaksperiode(periode) &&
                (periode.konto === StnadskontoType.Fedrekvote || periode.konto === StnadskontoType.Fellesperiode)
            ) {
                if (periode.konto === StnadskontoType.Fedrekvote) {
                    return {
                        ...periode,
                        konto: StnadskontoType.Mdrekvote,
                        forelder: Forelder.mor,
                    };
                } else {
                    return {
                        ...periode,
                        forelder: Forelder.mor,
                    };
                }
            }

            return periode;
        });

        return forslagGjortOmTilMor;
    }
};

const deltUttakAdopsjon = (
    famDato: Date,
    erFarEllerMedmor: boolean,
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    startdatoPermisjon: Date | undefined,
    fellesperiodeukerMor: number | undefined,
    harAnnenForelderSktFP: boolean | undefined,
    antallDagerFellesperiodeFarMedmor: number | undefined,
    antallUkerFellesperiodeFarMedmor: number | undefined,
    morSinSisteUttaksdag: Date | undefined,
    farSinFrsteUttaksdag: Date | undefined,
    begrunnelseForUtsettelse: UtsettelsersakType | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined
) => {
    if (!harAnnenForelderSktFP) {
        return deltUttakAdopsjonSktFrst(
            famDato,
            erFarEllerMedmor,
            tilgjengeligeStnadskontoer,
            startdatoPermisjon,
            fellesperiodeukerMor,
            harAnnenForelderSktFP,
            frsteUttaksdagNesteBarnsSak
        );
    } else {
        return deltUttakAdopsjonSktSist(
            famDato,
            tilgjengeligeStnadskontoer,
            erFarEllerMedmor,
            antallDagerFellesperiodeFarMedmor,
            antallUkerFellesperiodeFarMedmor,
            morSinSisteUttaksdag!,
            farSinFrsteUttaksdag!,
            begrunnelseForUtsettelse,
            frsteUttaksdagNesteBarnsSak
        );
    }
};

const deltUttakFdselMor = (
    famDato: Date,
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    nsketStartdatoPermisjon: Date | undefined,
    fellesperiodeukerMor: number | undefined
): Periode[] => {
    const frsteUttaksdag = Uttaksdagen(famDato).denneEllerNeste();
    const perioder: Periode[] = [];
    const skalHaForeldrePengerFrFdsel = nsketStartdatoPermisjon ? true : false;
    const fpFrFdselKonto: TilgjengeligStnadskonto | undefined = tilgjengeligeStnadskontoer.find(
        (konto) => konto.konto === StnadskontoType.ForeldrepengerFrFdsel
    );
    const mkKonto: TilgjengeligStnadskonto | undefined = tilgjengeligeStnadskontoer.find(
        (konto) => konto.konto === StnadskontoType.Mdrekvote
    );
    let currentTomDate: Date = frsteUttaksdag;
    if (fpFrFdselKonto !== undefined && skalHaForeldrePengerFrFdsel && nsketStartdatoPermisjon) {
        const startdatoPermisjon = Uttaksdagen(nsketStartdatoPermisjon).denneEllerNeste();
        const dagerFrFdsel = Uttaksdagen(startdatoPermisjon).getUttaksdagerFremTilDato(currentTomDate);
        const merEnnTreUkerPermisjonFrFdsel = dagerFrFdsel > 15;
        const startdatoFpFrFdsel = Uttaksdagen(frsteUttaksdag).trekkFra(
            merEnnTreUkerPermisjonFrFdsel ? 15 : dagerFrFdsel
        );

        if (merEnnTreUkerPermisjonFrFdsel) {
            const ekstraPeriodeFrFdsel: Periode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder: Forelder.mor,
                konto: StnadskontoType.Fellesperiode,
                tidsperiode: getTidsperiode(startdatoPermisjon, dagerFrFdsel - 15),
                vedlegg: [],
            };

            perioder.push(ekstraPeriodeFrFdsel);
        }

        const periodeFrFdsel: Periode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.mor,
            konto: StnadskontoType.ForeldrepengerFrFdsel,
            tidsperiode: {
                fom: startdatoFpFrFdsel,
                tom: Uttaksdagen(currentTomDate).forrige(),
            },
        };

        perioder.push(periodeFrFdsel);
    } else {
        const periodeFrFdsel: Periode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.mor,
            konto: StnadskontoType.ForeldrepengerFrFdsel,
            skalIkkeHaUttakFrTermin: true,
            tidsperiode: {} as any,
            vedlegg: [],
        };
        perioder.push(periodeFrFdsel);
    }

    if (mkKonto !== undefined) {
        const periodeMdrekvote: Periode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.mor,
            konto: StnadskontoType.Mdrekvote,
            tidsperiode: getTidsperiode(currentTomDate, mkKonto.dager),
            nskerSamtidigUttak: false,
            gradert: false,
        };

        currentTomDate = Uttaksdagen(periodeMdrekvote.tidsperiode.tom).neste();

        perioder.push(periodeMdrekvote);
    }

    if (fellesperiodeukerMor !== undefined && fellesperiodeukerMor > 0) {
        const ekstraPermisjonFrFdsel = perioder.find(
            (p) => isUttaksperiode(p) && p.konto === StnadskontoType.Fellesperiode
        );

        let trekkEkstraPermisjonDager = 0;
        if (ekstraPermisjonFrFdsel) {
            trekkEkstraPermisjonDager = Tidsperioden(ekstraPermisjonFrFdsel.tidsperiode).getAntallUttaksdager();
        }

        if (fellesperiodeukerMor * 5 - trekkEkstraPermisjonDager > 0) {
            const periodeFellesperiodeMor: Periode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder: Forelder.mor,
                konto: StnadskontoType.Fellesperiode,
                tidsperiode: getTidsperiode(currentTomDate, fellesperiodeukerMor * 5 - trekkEkstraPermisjonDager),
                nskerSamtidigUttak: false,
                gradert: false,
            };

            perioder.push(periodeFellesperiodeMor);
        }
    }

    return perioder.sort(sorterPerioder);
};

const deltUttakFdselFarMedmor = (
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    antallDagerFellesperiodeFarMedmor: number | undefined,
    antallUkerFellesperiodeFarMedmor: number | undefined,
    morSinSisteUttaksdag: Date,
    farSinFrsteUttaksdag: Date,
    begrunnelseForUtsettelse: UtsettelsersakType | undefined,
    familiehendelsesdato: Date,
    termindato: Date | undefined,
    morHarRettPForeldrepengerIES: boolean | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined,
    situasjon = 'fdsel'
): Periode[] => {
    if (
        !andreAugust2022ReglerGjelder(familiehendelsesdato) &&
        dateIsSameOrAfter(morSinSisteUttaksdag, farSinFrsteUttaksdag) &&
        !morHarRettPForeldrepengerIES
    ) {
        return [];
    }

    const perioder: Periode[] = [];
    const startDatoUttak = Uttaksdagen(farSinFrsteUttaksdag).denneEllerNeste();
    let sisteUttaksDag = Uttaksdagen(farSinFrsteUttaksdag).denneEllerNeste();
    const fedrekvoteKonto: TilgjengeligStnadskonto | undefined = tilgjengeligeStnadskontoer.find(
        (konto) => konto.konto === StnadskontoType.Fedrekvote
    );
    const fellesKonto: TilgjengeligStnadskonto | undefined = tilgjengeligeStnadskontoer.find(
        (konto) => konto.konto === StnadskontoType.Fellesperiode
    );

    const morHarRett = true;

    if (
        begrunnelseForUtsettelse &&
        morSinSisteUttaksdag !== undefined &&
        skalFarUtsetteEtterMorSinSisteUttaksdag(farSinFrsteUttaksdag, morSinSisteUttaksdag)
    ) {
        perioder.push({
            id: guid(),
            rsak: begrunnelseForUtsettelse,
            type: Periodetype.Utsettelse,
            forelder: Forelder.farMedmor,
            erArbeidstaker: false, // TODO
            tidsperiode: {
                fom: Uttaksdagen(morSinSisteUttaksdag).neste(),
                tom: Uttaksdagen(farSinFrsteUttaksdag).forrige(),
            },
        });
    }

    if (fedrekvoteKonto !== undefined) {
        const erPeriodeWLBRundtFdsel =
            situasjon === 'fdsel' &&
            andreAugust2022ReglerGjelder(familiehendelsesdato) &&
            starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel(
                getTidsperiode(startDatoUttak, 1),
                familiehendelsesdato,
                termindato
            );

        const lengdePForeslttUttak = erPeriodeWLBRundtFdsel
            ? getLengdePForeslttWLBUttakFarMedmor(familiehendelsesdato, startDatoUttak)
            : fedrekvoteKonto.dager;

        const fedrekvotePeriode: Periode = {
            id: guid(),
            type: Periodetype.Uttak,
            forelder: Forelder.farMedmor,
            konto: StnadskontoType.Fedrekvote,
            tidsperiode: getTidsperiode(startDatoUttak, lengdePForeslttUttak),
            nskerSamtidigUttak: erPeriodeWLBRundtFdsel,
            samtidigUttakProsent: erPeriodeWLBRundtFdsel ? '100' : undefined,
        };

        sisteUttaksDag = Uttaksdagen(fedrekvotePeriode.tidsperiode.tom).neste();

        if (
            farMedmorsTidsperiodeSkalSplittesPFamiliehendelsesdato(
                fedrekvotePeriode,
                familiehendelsesdato,
                morHarRett,
                termindato
            )
        ) {
            const fedrekvotePerioder = splittUttaksperiodePFamiliehendelsesdato(
                fedrekvotePeriode,
                familiehendelsesdato
            );
            fedrekvotePerioder.forEach((periode) => perioder.push(periode));
        } else if (
            frsteUttaksdagNesteBarnsSak !== undefined &&
            tidperiodeOverlapperDato(fedrekvotePeriode.tidsperiode, frsteUttaksdagNesteBarnsSak)
        ) {
            const fedrekvotePerioder = splittPeriodePDato(fedrekvotePeriode, frsteUttaksdagNesteBarnsSak);
            fedrekvotePerioder.forEach((periode) => perioder.push(periode));
        } else {
            perioder.push(fedrekvotePeriode);
        }
    }

    if (fellesKonto !== undefined) {
        let antallDagerFellesperiode = 0;

        if (antallUkerFellesperiodeFarMedmor !== undefined && antallUkerFellesperiodeFarMedmor !== 0) {
            antallDagerFellesperiode = 5 * antallUkerFellesperiodeFarMedmor;
        }

        if (antallDagerFellesperiodeFarMedmor !== undefined && antallDagerFellesperiodeFarMedmor !== 0) {
            antallDagerFellesperiode = antallDagerFellesperiode + antallDagerFellesperiodeFarMedmor;
        }

        if (antallDagerFellesperiode > 0) {
            const fellesPeriode: Periode = {
                id: guid(),
                type: Periodetype.Uttak,
                forelder: Forelder.farMedmor,
                konto: StnadskontoType.Fellesperiode,
                tidsperiode: getTidsperiode(sisteUttaksDag, antallDagerFellesperiode),
                nskerSamtidigUttak: false,
                gradert: false,
            };

            perioder.push(fellesPeriode);
        }
    }

    return perioder;
};

const deltUttakFdsel = (
    famDato: Date,
    erFarEllerMedmor: boolean,
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    startdatoPermisjon: Date | undefined,
    fellesperiodeukerMor: number | undefined,
    antallDagerFellesperiodeFarMedmor: number | undefined,
    antallUkerFellesperiodeFarMedmor: number | undefined,
    morSinSisteUttaksdag: Date | undefined,
    farSinFrsteUttaksdag: Date | undefined,
    begrunnelseForUtsettelse: UtsettelsersakType | undefined,
    annenForelderHarRettPForeldrepengerIES: boolean | undefined,
    frsteUttaksdagNesteBarnsSak: Date | undefined,
    termindato?: Date | undefined
) => {
    if (!erFarEllerMedmor) {
        return deltUttakFdselMor(famDato, tilgjengeligeStnadskontoer, startdatoPermisjon, fellesperiodeukerMor);
    } else {
        const tilgjengeligeStnadskontoerUtenFPP = tilgjengeligeStnadskontoer.filter(
            (konto) => konto.konto !== StnadskontoType.ForeldrepengerFrFdsel
        );

        return deltUttakFdselFarMedmor(
            tilgjengeligeStnadskontoerUtenFPP,
            antallDagerFellesperiodeFarMedmor,
            antallUkerFellesperiodeFarMedmor,
            morSinSisteUttaksdag!,
            farSinFrsteUttaksdag!,
            begrunnelseForUtsettelse,
            famDato,
            termindato,
            annenForelderHarRettPForeldrepengerIES,
            frsteUttaksdagNesteBarnsSak
        );
    }
};

export interface DeltUttakParams {
    situasjon: Situasjon;
    famDato: Date;
    erFarEllerMedmor: boolean;
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[];
    startdatoPermisjon: Date | undefined;
    fellesperiodeukerMor: number | undefined;
    harAnnenForelderSktFP: boolean | undefined;
    antallDagerFellesperiodeFarMedmor: number | undefined;
    antallUkerFellesperiodeFarMedmor: number | undefined;
    morSinSisteUttaksdag: Date | undefined;
    farSinFrsteUttaksdag: Date | undefined;
    begrunnelseForUtsettelse: UtsettelsersakType | undefined;
    frsteUttaksdagNesteBarnsSak: Date | undefined;
    annenForelderHarRettPForeldrepengerIES?: boolean | undefined;
    termindato?: Date | undefined;
}

export const deltUttak = (params: DeltUttakParams) => {
    if (params.situasjon === 'adopsjon') {
        return deltUttakAdopsjon(
            params.famDato,
            params.erFarEllerMedmor,
            params.tilgjengeligeStnadskontoer,
            params.startdatoPermisjon,
            params.fellesperiodeukerMor,
            params.harAnnenForelderSktFP,
            params.antallDagerFellesperiodeFarMedmor,
            params.antallUkerFellesperiodeFarMedmor,
            params.morSinSisteUttaksdag,
            params.farSinFrsteUttaksdag,
            params.begrunnelseForUtsettelse,
            params.frsteUttaksdagNesteBarnsSak
        );
    }

    if (params.situasjon === 'fdsel') {
        return deltUttakFdsel(
            params.famDato,
            params.erFarEllerMedmor,
            params.tilgjengeligeStnadskontoer,
            params.startdatoPermisjon,
            params.fellesperiodeukerMor,
            params.antallDagerFellesperiodeFarMedmor,
            params.antallUkerFellesperiodeFarMedmor,
            params.morSinSisteUttaksdag,
            params.farSinFrsteUttaksdag,
            params.begrunnelseForUtsettelse,
            params.annenForelderHarRettPForeldrepengerIES,
            params.frsteUttaksdagNesteBarnsSak,
            params.termindato
        );
    }

    return [];
};
