import { Block, intlUtils } from '@navikt/fp-common';
import InfoOmSknaden from 'app/components/info-eksisterende-sak/InfoOmSknaden';
import actionCreator from 'app/context/action/actionCreator';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import SknadRoutes from 'app/routes/routes';
import { getAntallUker } from 'app/steps/uttaksplan-info/utils/stnadskontoer';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { EksisterendeSak } from 'app/types/EksisterendeSak';
import { Forelder } from 'app/types/Forelder';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { getErMorUfr } from 'app/utils/annenForelderUtils';
import { getFamiliehendelsedato, getTermindato } from 'app/utils/barnUtils';
import { ISOStringToDate } from 'app/utils/dateUtils';
import { getDekningsgradFromString } from 'app/utils/getDekningsgradFromString';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useSknad from 'app/utils/hooks/useSknad';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { storeAppState } from 'app/utils/submitUtils';
import { lagUttaksplan } from 'app/utils/uttaksplan/lagUttaksplan';
import { Hovedknapp } from 'nav-frontend-knapper';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import { isInfoPeriode } from 'uttaksplan/types/Periode';
import FarMedmorsFrsteDag from '../sprsml/FarMedmorsFrsteDag';
import {
    FarMedmorFrstegangssknadMedAnnenPartFormComponents,
    FarMedmorFrstegangssknadMedAnnenPartFormData,
    FarMedmorFrstegangssknadMedAnnenPartFormField,
} from './farMedmorFrstegangssknadMedAnnenPartFormConfig';
import { farMedmorFrstegangssknadMedAnnenPartQuestionsConfig } from './farMedmorFrstegangssknadMedAnnenPartQuestionsConfig';
import { getFarMedmorFrstegangssknadMedAnnenPartInitialValues } from './farMedmorFrstegangssknadMedAnnenPartUtils';
import { getHarAktivitetskravIPeriodeUtenUttak } from 'app/utils/uttaksplan/uttaksplanUtils';
import { dateToISOString } from '@navikt/sif-common-formik/lib';
import { getMorHarRettPForeldrepengerINorgeEllerES } from 'app/utils/personUtils';
import { leggTilAnnenPartsPerioderISkerenesUttaksplan } from 'app/steps/uttaksplan-info/utils/leggTilAnnenPartsPerioderISkerensUttaksplan';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
    eksisterendeSakAnnenPart: EksisterendeSak | undefined;
}

const FarMedmorFrstegangssknadMedAnnenPart: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer100DTO,
    tilgjengeligeStnadskontoer80DTO,
    eksisterendeSakAnnenPart,
}) => {
    const sknad = useSknad();
    const { state } = useForeldrepengesknadContext();
    const intl = useIntl();
    const { barn, skersituasjon, annenForelder, erEndringssknad } = sknad;
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const lagretUttaksplanInfo = useUttaksplanInfo<FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo>();
    const familiehendelsedato = getFamiliehendelsedato(barn);
    const familiehendelsedatoDate = ISOStringToDate(familiehendelsedato);
    const erFdsel = skersituasjon.situasjon === 'fdsel';
    const erAdopsjon = skersituasjon.situasjon === 'adopsjon';
    const erMorUfr = getErMorUfr(annenForelder, erFarEllerMedmor);
    const bareFarHarRett = !getMorHarRettPForeldrepengerINorgeEllerES(
        skersituasjon.rolle,
        erFarEllerMedmor,
        annenForelder
    );
    const frsteUttaksdagNesteBarnsSak =
        state.barnFraNesteSak !== undefined ? state.barnFraNesteSak.startdatoFrsteStnadsperiode : undefined;
    const erDeltUttak = true;
    const termindato = getTermindato(barn);
    const harAktivitetskravIPeriodeUtenUttak = getHarAktivitetskravIPeriodeUtenUttak({
        erDeltUttak,
        morHarRett: true,
        skerErAleneOmOmsorg: false,
    });

    const morsSisteUttaksdag =
        eksisterendeSakAnnenPart && eksisterendeSakAnnenPart.uttaksplan.length > 0
            ? dateToISOString(
                  eksisterendeSakAnnenPart.uttaksplan[eksisterendeSakAnnenPart.uttaksplan.length - 1].tidsperiode.tom
              )
            : undefined;
    const onValidSubmitHandler = (values: Partial<FarMedmorFrstegangssknadMedAnnenPartFormData>) => {
        const uttaksplanInfo: FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo = {
            permisjonStartdato: values.permisjonStartdato!,
        };
        const stnadskontoer = tilgjengeligeStnadskontoer[getDekningsgradFromString(grunnlag.dekningsgrad)];
        const antallUker = getAntallUker(tilgjengeligeStnadskontoer[grunnlag.dekningsgrad]);
        const farMedmorSinePerioder = lagUttaksplan({
            annenForelderErUfr: erMorUfr,
            erDeltUttak,
            erEndringssknad,
            erEnkelEndringssknad: erEndringssknad,
            familiehendelsesdato: familiehendelsedatoDate!,
            frsteUttaksdagEtterSeksUker: Uttaksdagen(Uttaksdagen(familiehendelsedatoDate!).denneEllerNeste()).leggTil(
                30
            ),
            situasjon: erFdsel ? 'fdsel' : 'adopsjon',
            skerErFarEllerMedmor: erFarEllerMedmor,
            skerHarMidlertidigOmsorg: false,
            tilgjengeligeStnadskontoer: stnadskontoer,
            uttaksplanSkjema: {
                morSinSisteUttaksdag: morsSisteUttaksdag,
                farSinFrsteUttaksdag: values.permisjonStartdato,
                antallDagerFellesperiodeFarMedmor: undefined,
                antallUkerFellesperiodeFarMedmor: undefined,
            },
            bareFarMedmorHarRett: bareFarHarRett,
            termindato,
            harAktivitetskravIPeriodeUtenUttak,
            frsteUttaksdagNesteBarnsSak,
        });
        let uttaksplanMedAnnenPart;

        if (eksisterendeSakAnnenPart && farMedmorSinePerioder.length > 0) {
            uttaksplanMedAnnenPart = leggTilAnnenPartsPerioderISkerenesUttaksplan(
                eksisterendeSakAnnenPart.uttaksplan,
                farMedmorSinePerioder,
                familiehendelsedatoDate!,
                harAktivitetskravIPeriodeUtenUttak,
                erAdopsjon,
                bareFarHarRett,
                erFarEllerMedmor,
                frsteUttaksdagNesteBarnsSak
            );
        } else if (eksisterendeSakAnnenPart) {
            uttaksplanMedAnnenPart = eksisterendeSakAnnenPart.uttaksplan;
        } else {
            uttaksplanMedAnnenPart = farMedmorSinePerioder;
        }

        return [
            actionCreator.setAntallUkerIUttaksplan(antallUker),
            actionCreator.setUttaksplanInfo(uttaksplanInfo),
            actionCreator.setDekningsgrad(grunnlag.dekningsgrad),
            actionCreator.lagUttaksplanforslag(uttaksplanMedAnnenPart),
        ];
    };

    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        SknadRoutes.UTTAKSPLAN,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );

    if (!eksisterendeSakAnnenPart || !erFarEllerMedmor) {
        return null;
    }

    const navnMor = isAnnenForelderOppgitt(annenForelder) ? annenForelder.fornavn : '';
    const { grunnlag, uttaksplan } = eksisterendeSakAnnenPart;
    const morsPerioder = uttaksplan.filter((p) => isInfoPeriode(p) && p.forelder === Forelder.mor);
    const morsSisteDag = morsPerioder.reverse()[0].tidsperiode.tom;

    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO
    );

    return (
        <FarMedmorFrstegangssknadMedAnnenPartFormComponents.FormikWrapper
            initialValues={getFarMedmorFrstegangssknadMedAnnenPartInitialValues(lagretUttaksplanInfo)}
            onSubmit={handleSubmit}
            renderForm={({ values: formValues, setFieldValue }) => {
                const visibility = farMedmorFrstegangssknadMedAnnenPartQuestionsConfig.getVisbility(formValues);
                const valgtMengdeStnadskonto = tilgjengeligeStnadskontoer[grunnlag.dekningsgrad];

                return (
                    <FarMedmorFrstegangssknadMedAnnenPartFormComponents.Form
                        includeButtons={false}
                        includeValidationSummary={true}
                    >
                        <Block padBottom="l">
                            <InfoOmSknaden
                                eksisterendeSak={eksisterendeSakAnnenPart}
                                erIUttaksplanenSteg={false}
                                tilgjengeligeStnadskontoer={valgtMengdeStnadskonto}
                            />
                        </Block>
                        <Block padBottom="l">
                            <FarMedmorsFrsteDag
                                FormComponents={FarMedmorFrstegangssknadMedAnnenPartFormComponents}
                                fieldName={FarMedmorFrstegangssknadMedAnnenPartFormField.permisjonStartdato}
                                familiehendelsesdato={familiehendelsedatoDate!}
                                setFieldValue={setFieldValue}
                                morsSisteDag={morsSisteDag}
                                navnMor={navnMor}
                                termindato={termindato}
                                situasjon={skersituasjon.situasjon}
                                morHarRettTilForeldrepengerIES={false}
                            />
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp disabled={isSubmitting} spinner={isSubmitting}>
                                {intlUtils(intl, 'sknad.gVidere')}
                            </Hovedknapp>
                        </Block>
                    </FarMedmorFrstegangssknadMedAnnenPartFormComponents.Form>
                );
            }}
        />
    );
};

export default FarMedmorFrstegangssknadMedAnnenPart;
