import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import React, { FunctionComponent } from 'react';
import FarMedmorFdselFrsteganggsknadBeggeHarRett from './scenarios/far-medmor-fdsel-begge-har-rett/FarMedmorFdselBeggeHarRett';
import MorFdsel from './scenarios/mor-fodsel/MorFdsel';
import MorFarAdopsjon from './scenarios/mor-far-adopsjon/MorFarAdopsjon';
import MorFarAdopsjonAnnenForelderHarRettIES from './scenarios/mor-far-adopsjon-annen-part-har-rett-i-es/MorFarAdopsjonAnnenForelderHarRettIES';
import MorFarFdselAnnenForelderHarRettIES from './scenarios/mor-far-fdsel-annen-part-har-rett-i-es/MorFarFdselAnnenForelderHarRettIES';
import FarMedmorAleneomsorgFdselAdopsjon from './scenarios/far-medmor-aleneomsorg-fdsel/FarMedmorAleneomsorgFdsel';
import FarMedmorFdselOgMorHarIkkeRett from './scenarios/far-medmor-fdsel-og-mor-har-ikke-rett/FarMedmorFdselOgMorHarIkkeRett';
import { EksisterendeSak } from 'app/types/EksisterendeSak';
import FarMedmorFrstegangssknadMedAnnenPart from './scenarios/farMedmor-frstegangssknad-med-annen-part/FarMedmorFrstegangssknadMedAnnenPart';
import { getUttaksplanScenario } from './scenarios/scenarios';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import useSknad from 'app/utils/hooks/useSknad';
import { harAnnenForelderRettIES } from 'app/utils/annenForelderUtils';
interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
    eksisterendeSakAnnenPart: EksisterendeSak | undefined;
}

const UttaksplanInfoScenarios: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer100DTO,
    tilgjengeligeStnadskontoer80DTO,
    eksisterendeSakAnnenPart,
}) => {
    const { skersituasjon, sker, annenForelder } = useSknad();
    const erFdsel = skersituasjon.situasjon === 'fdsel';
    const erAdopsjon = skersituasjon.situasjon === 'adopsjon';
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const annenForelderHarRett = isAnnenForelderOppgitt(annenForelder)
        ? !!annenForelder.harRettPForeldrepengerINorge || !!annenForelder.harRettPForeldrepengerIES
        : false;
    const annenForelderOppgittIkkeAleneOmOmsorg = isAnnenForelderOppgitt(annenForelder)
        ? annenForelder.harRettPForeldrepengerINorge !== undefined
        : false;
    const annenForelderHarRettIES = harAnnenForelderRettIES(annenForelder);

    const scenario = getUttaksplanScenario({
        erFdsel,
        erFarEllerMedmor,
        skerErAleneOmOmsorg: !!sker.erAleneOmOmsorg,
        annenForelderKanIkkeOppgis: annenForelder.kanIkkeOppgis,
        annenForelderHarRett: annenForelderHarRett,
        erAdopsjon,
        eksisterendeSakAnnenPart,
        annenForelderOppgittIkkeAleneOmOmsorg,
        annenForelderHarRettIES,
    });

    switch (scenario) {
        case 'farMedmorAleneomsorgFdselAdopsjon':
            return (
                <FarMedmorAleneomsorgFdselAdopsjon
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                />
            );
        case 'farMedmorFdselBeggeHarRett':
            return (
                <FarMedmorFdselFrsteganggsknadBeggeHarRett
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                    eksisterendeSakAnnenPart={eksisterendeSakAnnenPart}
                />
            );
        case 'farMedmorFdselMorHarIkkeRett':
            return (
                <FarMedmorFdselOgMorHarIkkeRett
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                />
            );
        case 'farMedmorFrstegangssknadMedAnnenPart':
            return (
                <FarMedmorFrstegangssknadMedAnnenPart
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                    eksisterendeSakAnnenPart={eksisterendeSakAnnenPart}
                />
            );
        case 'morFarAdopsjon':
            return (
                <MorFarAdopsjon
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                />
            );
        case 'morFdsel':
            return (
                <MorFdsel
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                    eksisterendeSakFar={eksisterendeSakAnnenPart}
                />
            );
        case 'morFarFdselAnnenForelderHarRettIES':
            return (
                <MorFarFdselAnnenForelderHarRettIES
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                />
            );
        case 'morFarAdopsjonAnnenForelderHarRettIES':
            return (
                <MorFarAdopsjonAnnenForelderHarRettIES
                    tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                    tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                />
            );
    }
};

export default UttaksplanInfoScenarios;
