import { Situasjon } from 'app/types/Situasjon';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { UttaksplanSkjemadata } from 'app/types/UttaksplanSkjemaData';
import { finnOgSettInnHull } from 'uttaksplan/builder/uttaksplanbuilderUtils';
import { Periode } from 'uttaksplan/types/Periode';
import { ISOStringToDate } from '../dateUtils';
import { deltUttak } from './deltUttak';
import { ikkeDeltUttak } from './ikkeDeltUttak';

export interface LagUttaksplanParams {
    situasjon: Situasjon;
    familiehendelsesdato: Date;
    erDeltUttak: boolean;
    erEndringssknad: boolean;
    skerErFarEllerMedmor: boolean;
    annenForelderErUfr: boolean;
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[];
    uttaksplanSkjema: UttaksplanSkjemadata;
    erEnkelEndringssknad: boolean;
    frsteUttaksdagEtterSeksUker: Date;
    skerHarMidlertidigOmsorg: boolean;
    bareFarMedmorHarRett: boolean;
    harAktivitetskravIPeriodeUtenUttak: boolean;
    termindato: Date | undefined;
    frsteUttaksdagNesteBarnsSak: Date | undefined;
    annenForelderHarRettPForeldrepengerIES?: boolean;
}

export const lagUttaksplan = (params: LagUttaksplanParams): Periode[] => {
    const {
        situasjon,
        familiehendelsesdato,
        erDeltUttak,
        erEndringssknad,
        skerErFarEllerMedmor,
        annenForelderErUfr,
        tilgjengeligeStnadskontoer,
        uttaksplanSkjema,
        bareFarMedmorHarRett,
        termindato,
        harAktivitetskravIPeriodeUtenUttak,
        annenForelderHarRettPForeldrepengerIES,
        frsteUttaksdagNesteBarnsSak,
    } = params;

    if (uttaksplanSkjema.nskerIkkeFlerePerioder || erEndringssknad) {
        return [];
    }
    const erAdopsjon = situasjon === 'adopsjon';

    const {
        harAnnenForelderSktFP,
        startdatoPermisjon,
        fellesperiodeukerMor,
        antallDagerFellesperiodeFarMedmor,
        antallUkerFellesperiodeFarMedmor,
        morSinSisteUttaksdag,
        farSinFrsteUttaksdag,
        begrunnelseForUtsettelse,
    } = uttaksplanSkjema;

    const morSinSisteUttaksdagDate = ISOStringToDate(morSinSisteUttaksdag);
    if (familiehendelsesdato) {
        if (erDeltUttak) {
            const forslag = deltUttak({
                situasjon,
                famDato: familiehendelsesdato,
                erFarEllerMedmor: skerErFarEllerMedmor,
                tilgjengeligeStnadskontoer,
                startdatoPermisjon: ISOStringToDate(startdatoPermisjon),
                fellesperiodeukerMor,
                harAnnenForelderSktFP,
                antallDagerFellesperiodeFarMedmor,
                antallUkerFellesperiodeFarMedmor,
                morSinSisteUttaksdag: morSinSisteUttaksdagDate,
                farSinFrsteUttaksdag: ISOStringToDate(farSinFrsteUttaksdag),
                begrunnelseForUtsettelse,
                annenForelderHarRettPForeldrepengerIES,
                termindato,
                frsteUttaksdagNesteBarnsSak,
            });

            return finnOgSettInnHull(
                forslag,
                harAktivitetskravIPeriodeUtenUttak,
                familiehendelsesdato,
                erAdopsjon,
                skerErFarEllerMedmor && !erDeltUttak,
                skerErFarEllerMedmor,
                frsteUttaksdagNesteBarnsSak
            );
        } else {
            const forslag = ikkeDeltUttak(
                situasjon,
                familiehendelsesdato,
                skerErFarEllerMedmor,
                tilgjengeligeStnadskontoer,
                ISOStringToDate(startdatoPermisjon),
                annenForelderErUfr,
                bareFarMedmorHarRett,
                termindato,
                frsteUttaksdagNesteBarnsSak
            );

            return finnOgSettInnHull(
                forslag,
                harAktivitetskravIPeriodeUtenUttak,
                familiehendelsesdato,
                erAdopsjon,
                skerErFarEllerMedmor && !erDeltUttak,
                skerErFarEllerMedmor,
                frsteUttaksdagNesteBarnsSak
            );
        }
    }

    return [];
};
