import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import React, { FunctionComponent } from 'react';
import Veilederpanel from 'nav-frontend-veilederpanel';
import VeilederNormal from 'app/assets/VeilederNormal';
import { getNavnGenitivEierform } from 'app/utils/personUtils';
import { Block, intlUtils } from '@navikt/fp-common';
import useSknad from 'app/utils/hooks/useSknad';
import { FormattedMessage, useIntl } from 'react-intl';
import {
    FarMedmorFdselBeggeHarRettFormComponents,
    FarMedmorFdselBeggeHarRettFormData,
    FarMedmorFdselBeggeHarRettFormField,
} from './farMedmorFdselBeggeHarRettFormConfig';
import {
    getInitialFarMedmorFdselBeggeHarRettValues,
    mapFarMedmorFdselBeggeHarRettToState,
} from './farMedmorFdselBeggeHarRettUtils';
import TilgjengeligeDagerGraf from '../../tilgjengeligeDagerGraf/TilgjengeligeDagerGraf';
import { getTilgjengeligeDager } from '../../tilgjengeligeDagerGraf/tilgjengeligeDagerUtils';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { getFamiliehendelsedato, getTermindato } from 'app/utils/barnUtils';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import farMedmorFdselBeggeHarRettQuestionsConfig from './farMedmorFdselBeggeHarRettQuestionsConfig';
import MorsSisteDagSprsml from '../sprsml/MorsSisteDagSprsml';
import FarMedmorsFrsteDag from '../sprsml/FarMedmorsFrsteDag';
import AntallUkerOgDagerFellesperiodeFarMedmorSprsml from '../sprsml/AntallUkerOgDagerFellesperiodeFarMedmorSprsml';
import { getErMorUfr } from 'app/utils/annenForelderUtils';
import { Forelder } from 'app/types/Forelder';
import { EksisterendeSak } from 'app/types/EksisterendeSak';
import DekningsgradSprsml from '../sprsml/DekningsgradSprsml';
import { Hovedknapp } from 'nav-frontend-knapper';
import actionCreator from 'app/context/action/actionCreator';
import SknadRoutes from 'app/routes/routes';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import { FarMedmorFdselBeggeHarRettUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { getDekningsgradFromString } from 'app/utils/getDekningsgradFromString';
import { lagUttaksplan } from 'app/utils/uttaksplan/lagUttaksplan';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { storeAppState } from 'app/utils/submitUtils';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { andreAugust2022ReglerGjelder, ISOStringToDate } from 'app/utils/dateUtils';
import { getAntallUker } from 'app/steps/uttaksplan-info/utils/stnadskontoer';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
    eksisterendeSakAnnenPart: EksisterendeSak | undefined;
}

const FarMedmorFdselFrsteganggsknadBeggeHarRett: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer100DTO,
    tilgjengeligeStnadskontoer80DTO,
}) => {
    const intl = useIntl();
    const { state } = useForeldrepengesknadContext();
    const { annenForelder, skersituasjon, barn, dekningsgrad, erEndringssknad } = useSknad();
    const { person } = useSkerinfo();
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const erFdsel = skersituasjon.situasjon === 'fdsel';
    const lagretUttaksplanInfo = useUttaksplanInfo<FarMedmorFdselBeggeHarRettUttaksplanInfo>();

    const navnFar = erFarEllerMedmor
        ? person.fornavn
        : isAnnenForelderOppgitt(annenForelder)
        ? annenForelder.fornavn
        : '';
    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const navnMor = erFarEllerMedmor && isAnnenForelderOppgitt(annenForelder) ? annenForelder.fornavn : person.fornavn;

    const erMorUfr = getErMorUfr(annenForelder, erFarEllerMedmor);
    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO
    );

    const familiehendelsesdatoDate = ISOStringToDate(familiehendelsesdato);
    const termindato = getTermindato(barn);
    const frsteUttaksdagNesteBarnsSak =
        state.barnFraNesteSak !== undefined ? state.barnFraNesteSak.startdatoFrsteStnadsperiode : undefined;
    const onValidSubmitHandler = (values: Partial<FarMedmorFdselBeggeHarRettFormData>) => {
        const antallUker = getAntallUker(tilgjengeligeStnadskontoer[values.dekningsgrad! === '100' ? 100 : 80]);
        return [
            actionCreator.setAntallUkerIUttaksplan(antallUker),
            actionCreator.setUttaksplanInfo(mapFarMedmorFdselBeggeHarRettToState(values)),
            actionCreator.setDekningsgrad(getDekningsgradFromString(values.dekningsgrad)),
            actionCreator.lagUttaksplanforslag(
                lagUttaksplan({
                    annenForelderErUfr: erMorUfr,
                    erDeltUttak: true,
                    erEndringssknad,
                    erEnkelEndringssknad: erEndringssknad,
                    familiehendelsesdato: familiehendelsesdatoDate!,
                    frsteUttaksdagEtterSeksUker: Uttaksdagen(
                        Uttaksdagen(familiehendelsesdatoDate!).denneEllerNeste()
                    ).leggTil(30),
                    situasjon: erFdsel ? 'fdsel' : 'adopsjon',
                    skerErFarEllerMedmor: erFarEllerMedmor,
                    skerHarMidlertidigOmsorg: false,
                    tilgjengeligeStnadskontoer:
                        tilgjengeligeStnadskontoer[getDekningsgradFromString(values.dekningsgrad)],
                    uttaksplanSkjema: {
                        morSinSisteUttaksdag: values.morsSisteDag,
                        farSinFrsteUttaksdag: values.farMedmorsFrsteDag,
                        antallDagerFellesperiodeFarMedmor: parseInt(values.antallDagerFellesperiode || '0', 10),
                        antallUkerFellesperiodeFarMedmor: parseInt(values.antallUkerFellesperiode || '0', 10),
                    },
                    bareFarMedmorHarRett: false,
                    termindato,
                    harAktivitetskravIPeriodeUtenUttak: false,
                    frsteUttaksdagNesteBarnsSak,
                })
            ),
        ];
    };
    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        SknadRoutes.UTTAKSPLAN,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );

    return (
        <FarMedmorFdselBeggeHarRettFormComponents.FormikWrapper
            initialValues={getInitialFarMedmorFdselBeggeHarRettValues(lagretUttaksplanInfo, dekningsgrad)}
            onSubmit={handleSubmit}
            renderForm={({ values: formValues, setFieldValue }) => {
                const visibility = farMedmorFdselBeggeHarRettQuestionsConfig.getVisbility({
                    ...formValues,
                    familiehendelsesdato: familiehendelsesdatoDate!,
                });

                const valgtStnadskonto = tilgjengeligeStnadskontoer[formValues.dekningsgrad === '100' ? 100 : 80];
                const tilgjengeligeDager = valgtStnadskonto
                    ? getTilgjengeligeDager(valgtStnadskonto, false, Forelder.farMedmor)
                    : undefined;

                return (
                    <FarMedmorFdselBeggeHarRettFormComponents.Form
                        includeButtons={false}
                        includeValidationSummary={true}
                    >
                        <Block padBottom="l">
                            <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                <FormattedMessage
                                    id="uttaksplaninfo.veileder.informasjonTilAnnenForelder"
                                    values={{
                                        navn: isAnnenForelderOppgitt(annenForelder)
                                            ? getNavnGenitivEierform(annenForelder.fornavn, intl.locale)
                                            : '',
                                    }}
                                />
                            </Veilederpanel>
                        </Block>
                        <Block padBottom="l">
                            <DekningsgradSprsml
                                FormKomponent={FarMedmorFdselBeggeHarRettFormComponents}
                                dekningsgradFeltNavn={FarMedmorFdselBeggeHarRettFormField.dekningsgrad}
                                tilgjengeligeStnadskontoer={tilgjengeligeStnadskontoer}
                                erDeltUttak={true}
                            />
                        </Block>
                        <Block visible={formValues.dekningsgrad !== ''}>
                            {tilgjengeligeDager && (
                                <TilgjengeligeDagerGraf
                                    erDeltUttak={true}
                                    erFarEllerMedmor={true}
                                    navnFarMedmor={navnFar}
                                    navnMor={navnMor}
                                    tilgjengeligeDager={tilgjengeligeDager}
                                />
                            )}
                        </Block>
                        <Block
                            padBottom="l"
                            visible={
                                erFarEllerMedmor &&
                                formValues.dekningsgrad !== '' &&
                                !andreAugust2022ReglerGjelder(ISOStringToDate(familiehendelsesdato)!)
                            }
                        >
                            <Veilederpanel svg={<VeilederNormal transparentBackground={true} />}>
                                <FormattedMessage
                                    id="uttaksplaninfo.veileder.farMedmor.infoOmTidsromMellomMorsSisteDagOgFarsFrsteDag"
                                    values={{ navnMor }}
                                />
                            </Veilederpanel>
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isVisible(FarMedmorFdselBeggeHarRettFormField.morsSisteDag)}
                        >
                            <MorsSisteDagSprsml
                                FormComponents={FarMedmorFdselBeggeHarRettFormComponents}
                                fieldName={FarMedmorFdselBeggeHarRettFormField.morsSisteDag}
                                navnMor={navnMor}
                                familiehendelsesdato={familiehendelsesdato}
                            />
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isVisible(FarMedmorFdselBeggeHarRettFormField.farMedmorsFrsteDag)}
                        >
                            <FarMedmorsFrsteDag
                                FormComponents={FarMedmorFdselBeggeHarRettFormComponents}
                                fieldName={FarMedmorFdselBeggeHarRettFormField.farMedmorsFrsteDag}
                                familiehendelsesdato={familiehendelsesdatoDate!}
                                setFieldValue={setFieldValue}
                                morsSisteDag={ISOStringToDate(formValues.morsSisteDag)}
                                navnMor={navnMor}
                                termindato={termindato}
                                situasjon={skersituasjon.situasjon}
                                morHarRettTilForeldrepengerIES={false}
                            />
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isVisible(
                                FarMedmorFdselBeggeHarRettFormField.antallDagerFellesperiode
                            )}
                        >
                            {tilgjengeligeDager && (
                                <AntallUkerOgDagerFellesperiodeFarMedmorSprsml
                                    FormComponents={FarMedmorFdselBeggeHarRettFormComponents}
                                    ukerFieldName={FarMedmorFdselBeggeHarRettFormField.antallUkerFellesperiode}
                                    dagerFieldName={FarMedmorFdselBeggeHarRettFormField.antallDagerFellesperiode}
                                    antallDager={formValues.antallDagerFellesperiode}
                                    antallUker={formValues.antallUkerFellesperiode}
                                    setFieldValue={setFieldValue}
                                    ukerMedFellesperiode={tilgjengeligeDager.dagerFelles / 5}
                                />
                            )}
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp disabled={isSubmitting} spinner={isSubmitting}>
                                {intlUtils(intl, 'sknad.gVidere')}
                            </Hovedknapp>
                        </Block>
                    </FarMedmorFdselBeggeHarRettFormComponents.Form>
                );
            }}
        />
    );
};

export default FarMedmorFdselFrsteganggsknadBeggeHarRett;
