import { dateToISOString } from '@navikt/sif-common-formik/lib';
import { MorFarFdselAnnenForelderHarRettIESUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import {
    MorFarFdselAnnenForelderHarRettIESFormField,
    MorFarFdselAnnenForelderHarRettIESFormData,
} from './morFarFdselAnnenForelderHarRettIESFormConfig';

const initialMorFarFdselAnnenForelderHarRettIESValues: MorFarFdselAnnenForelderHarRettIESFormData = {
    [MorFarFdselAnnenForelderHarRettIESFormField.dekningsgrad]: '',
    [MorFarFdselAnnenForelderHarRettIESFormField.permisjonStartdato]: '',
    [MorFarFdselAnnenForelderHarRettIESFormField.skalIkkeHaUttakFrTermin]: undefined,
};
export const mapMorFarFdselAnnenForelderHarRettIESFormToState = (
    values: Partial<MorFarFdselAnnenForelderHarRettIESFormData>
): MorFarFdselAnnenForelderHarRettIESUttaksplanInfo => {
    return {
        dekningsgrad: values.dekningsgrad!,
        permisjonStartdato: values.permisjonStartdato!,
        skalIkkeHaUttakFrTermin: values.skalIkkeHaUttakFrTermin,
    };
};
export const getInitialMorFarFdselAnnenForelderHarRettIESValues = (
    defaultPermisjonStartdato: Date,
    lagretUttaksplanInfo: MorFarFdselAnnenForelderHarRettIESUttaksplanInfo | undefined,
    dekningsgrad: Dekningsgrad
): MorFarFdselAnnenForelderHarRettIESFormData => {
    if (lagretUttaksplanInfo) {
        return {
            ...lagretUttaksplanInfo,
            [MorFarFdselAnnenForelderHarRettIESFormField.dekningsgrad]: dekningsgrad,
        };
    }
    return {
        ...initialMorFarFdselAnnenForelderHarRettIESValues,
        [MorFarFdselAnnenForelderHarRettIESFormField.permisjonStartdato]: dateToISOString(defaultPermisjonStartdato),
    };
};
