import { hasValue } from '@navikt/fp-common';
import { QuestionConfig, Questions } from '@navikt/sif-common-question-config/lib';
import { andreAugust2022ReglerGjelder, ISOStringToDate } from 'app/utils/dateUtils';
import { getSisteUttaksdag6UkerEtterFdsel } from 'app/utils/wlbUtils';
import dayjs from 'dayjs';
import {
    FarMedmorFdselBeggeHarRettFormData,
    FarMedmorFdselBeggeHarRettFormField,
} from './farMedmorFdselBeggeHarRettFormConfig';

interface FarMedmorFdselBeggeHarRettFormPayload extends FarMedmorFdselBeggeHarRettFormData {
    familiehendelsesdato: Date;
}
const includeFellesperiodeSprsml = (farMedmorsFrsteDag: string, familiehendelsesdato: Date) => {
    return (
        hasValue(farMedmorsFrsteDag) &&
        (!andreAugust2022ReglerGjelder ||
            dayjs(ISOStringToDate(farMedmorsFrsteDag)).isAfter(
                getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato),
                'day'
            ))
    );
};
const FarMedmorFdselBeggeHarRettFormConfig: QuestionConfig<
    FarMedmorFdselBeggeHarRettFormPayload,
    FarMedmorFdselBeggeHarRettFormField
> = {
    [FarMedmorFdselBeggeHarRettFormField.dekningsgrad]: {
        isIncluded: () => true,
        isAnswered: ({ dekningsgrad }) => hasValue(dekningsgrad),
    },
    [FarMedmorFdselBeggeHarRettFormField.morsSisteDag]: {
        isIncluded: ({ familiehendelsesdato }) => !andreAugust2022ReglerGjelder(familiehendelsesdato),
        isAnswered: ({ morsSisteDag, familiehendelsesdato }) =>
            andreAugust2022ReglerGjelder(familiehendelsesdato) || hasValue(morsSisteDag),
        visibilityFilter: ({ dekningsgrad }) => hasValue(dekningsgrad),
    },
    [FarMedmorFdselBeggeHarRettFormField.farMedmorsFrsteDag]: {
        isIncluded: ({ morsSisteDag, familiehendelsesdato }) =>
            andreAugust2022ReglerGjelder(familiehendelsesdato) || hasValue(morsSisteDag),
        isAnswered: ({ farMedmorsFrsteDag }) => hasValue(farMedmorsFrsteDag),
    },
    [FarMedmorFdselBeggeHarRettFormField.antallDagerFellesperiode]: {
        isIncluded: ({ farMedmorsFrsteDag, familiehendelsesdato }) =>
            includeFellesperiodeSprsml(farMedmorsFrsteDag, familiehendelsesdato),
        isAnswered: () => true,
    },
    [FarMedmorFdselBeggeHarRettFormField.antallUkerFellesperiode]: {
        isIncluded: ({ farMedmorsFrsteDag, familiehendelsesdato }) =>
            includeFellesperiodeSprsml(farMedmorsFrsteDag, familiehendelsesdato),

        isAnswered: () => true,
    },
};

const farMedmorFdselBeggeHarRettQuestionsConfig = Questions<
    FarMedmorFdselBeggeHarRettFormPayload,
    FarMedmorFdselBeggeHarRettFormField
>(FarMedmorFdselBeggeHarRettFormConfig);

export default farMedmorFdselBeggeHarRettQuestionsConfig;
