import { QuestionConfig, Questions } from '@navikt/sif-common-question-config';
import { hasValue } from '@navikt/fp-common';
import {
    FarMedmorFdselOgMorHarIkkeRettFormData,
    FarMedmorFdselOgMorHarIkkeRettFormField,
} from './farMedmorFdselOgMorHarIkkeRettFormConfig';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';

interface FarMedmorFdselOgMorHarIkkeRettQuestionsPayload extends FarMedmorFdselOgMorHarIkkeRettFormData {
    erMorUfr: boolean;
    familiehendelsesdato: Date;
}

const FarMedmorFdselOgMorHarIkkeRettFormConfig: QuestionConfig<
    FarMedmorFdselOgMorHarIkkeRettQuestionsPayload,
    FarMedmorFdselOgMorHarIkkeRettFormField
> = {
    [FarMedmorFdselOgMorHarIkkeRettFormField.dekningsgrad]: {
        isAnswered: ({ dekningsgrad }) => hasValue(dekningsgrad),
        isIncluded: () => true,
    },
    [FarMedmorFdselOgMorHarIkkeRettFormField.permisjonStartdato]: {
        isAnswered: ({ permisjonStartdato }) => hasValue(permisjonStartdato),
        isIncluded: ({ dekningsgrad, erMorUfr, familiehendelsesdato }) =>
            andreAugust2022ReglerGjelder(familiehendelsesdato) || (hasValue(dekningsgrad) && erMorUfr),
    },
};

export const farMedmorFdselOgMorHarIkkeRettQuestionsConfig = Questions<
    FarMedmorFdselOgMorHarIkkeRettQuestionsPayload,
    FarMedmorFdselOgMorHarIkkeRettFormField
>(FarMedmorFdselOgMorHarIkkeRettFormConfig);
