import { intlUtils } from '@navikt/fp-common';
import AnnenForelder, { AnnenForelderOppgitt, isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { RettighetType } from 'app/types/RettighetType';
import { Sak } from 'app/types/Sak';
import { IntlShape } from 'react-intl';

export const getErMorUfr = (annenForelder: AnnenForelder, erFarEllerMedmor: boolean) => {
    if (isAnnenForelderOppgitt(annenForelder) && erFarEllerMedmor) {
        return !!annenForelder.erUfr;
    }

    return false;
};

export const harAnnenForelderRettIES = (annenForelder: AnnenForelder): boolean => {
    return isAnnenForelderOppgitt(annenForelder) && !!annenForelder.harRettPForeldrepengerIES;
};

export const getMockAnnenForelder = (sak: Sak, intl: IntlShape): AnnenForelderOppgitt => {
    return {
        fornavn: intlUtils(intl, 'annen.forelder'),
        etternavn: '',
        fnr: '',
        harRettPForeldrepengerINorge:
            sak.rettighetType === RettighetType.BEGGE_RETT && !sak.harAnnenForelderTilsvarendeRettES,
        harRettPForeldrepengerIES: sak.harAnnenForelderTilsvarendeRettES,
        erUfr: (!sak.sakTilhrerMor && sak.morUfretrygd) || undefined,
        kanIkkeOppgis: false,
    };
};
