import { Situasjon } from 'app/types/Situasjon';
import dayjs from 'dayjs';

export const skalViseInfoOmPrematuruker = (
    fdselsdato: Date | undefined,
    termindato: Date | undefined,
    situasjon: Situasjon
): boolean => {
    if (fdselsdato === undefined || termindato === undefined || situasjon !== 'fdsel') {
        return false;
    }
    const fdselsdatoEtterEllerLikFrsteJuli = dayjs(fdselsdato).isSameOrAfter(dayjs(new Date('2019-07-01')), 'day');
    return (
        dayjs(fdselsdato).add(7, 'weeks').add(3, 'days').isBefore(dayjs(termindato), 'days') &&
        fdselsdatoEtterEllerLikFrsteJuli
    );
};
